import { Component, OnInit } from '@angular/core';
import {AdminPanelService} from "../admin-panel/admin-panel.service.ts.service"
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import {AdminAuthService} from "../admin.auth.service"
import {agentSignup} from "../../Model/agentSignup"
@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css','../Admin-common.css']
})
export class AddUserComponent implements OnInit {
  user: any;
  uid: any;
  username: any;
  image: any;
  agentSignup:agentSignup = new agentSignup()
  imageUplaod:boolean = true
  constructor(public adminPanelService:AdminPanelService,private _Activatedroute: ActivatedRoute,
    private _location: Location,public AdminAuthService:AdminAuthService) { }

  ngOnInit() {
    this.user  = JSON.parse(localStorage.getItem("user"));
this.getUserDetails()
  }
  getUserDetails()
  {

    if(this.user != null)
    {
      this.uid = this.user.uid
      document.getElementById("appfooter").style.display = "none"
      document.getElementById("header-main").style.display = "none"
      this.adminPanelService.getUser(this.uid).then((element)=>{
      this.username = element.data().Name
      this.image = element.data().Image
    })
  }
  }
  backClicked() {
    this._location.back();
  }



  createUser(title,Name,email,password,DOB,postcode,country,Town,streetname)
  {
    this.AdminAuthService.adminSignUp(Name,email,password,DOB)
  }
}
