import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-buyer-listing-detail',
  templateUrl: './buyer-listing-detail.component.html',
  styleUrls: ['./buyer-listing-detail.component.css',"../../../../Misc/CommonCode/seeDetails.css"  ]
})
export class BuyerListingDetailComponent implements OnInit {
  userDetails:any
  BuyerLookingpostcode: any;
  LookingStreetname: any;
  buyerPropertyFor: any;
  MinAmount: any;
  MaxAmount: any;
  buyingPosition: any;
  buyerFinancialPosition: any;
  BuyerPropertyType: any;
  BuyerRoommin: any;
  BuyerRoomsmax: any;
  constructor(private _router: Router) { }

  ngOnInit() {

    this.userDetails = JSON.parse(localStorage.getItem("BuyerListingDetail"))
    this.BuyerLookingpostcode  = this.userDetails.PropertyDetail.Lookkingpostcode
    this.LookingStreetname = this.userDetails.PropertyDetail.LookingStreetname
    this.buyerPropertyFor = this.userDetails.PropertyDetail.PropertyFor
    this.MinAmount = this.userDetails.PropertyDetail.MinAmount
    this.MaxAmount = this.userDetails.PropertyDetail.MaxAmount
    this.buyingPosition = this.userDetails.PropertyDetail.Position
    this.buyerFinancialPosition = this.userDetails.PropertyDetail.FinancialPosition
    this.BuyerPropertyType  = this.userDetails.PropertyDetail.PropertyType
    this.BuyerLookingpostcode = this.userDetails.PropertyDetail.Lookingpostcode
    this.BuyerRoommin = this.userDetails.PropertyDetail.Roommin
    this.BuyerRoomsmax = this.userDetails.PropertyDetail.Roomsmax
  }

  backClicked() {
    localStorage.removeItem("BuyerListingDetail");
    this._router.navigate(["/myListing/fav/tabdrop"])
  }


}
