import { Component, OnInit, Inject } from '@angular/core';
import { VERSION, MatDialogRef, MatDialog, MatSnackBar, MAT_DIALOG_DATA } from '@angular/material';
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-agentDataSubmission',
  templateUrl: './agentSubmission.component.html'
})
export class AgentSubmissionComponent {
  message: string = ""
  cancelButtonText = "Cancel"
  constructor(
    @Inject(MAT_DIALOG_DATA) private data: any,
    private _router: Router,
    private dialogRef: MatDialogRef<AgentSubmissionComponent >) {
    if (data) {
      this.message = data.message || this.message;
      if (data.buttonText) {
        this.cancelButtonText = data.buttonText.cancel || this.cancelButtonText;
      }
    }
    this.dialogRef.updateSize('300vw','300vw')
  }

  onConfirmClick(): void {
    this._router.navigate(["/myListing/myNestimate/action"]).then(()=>{
      window.location.reload();
    })
  }

}