import { Component, OnInit } from '@angular/core';
import {AgenthomeService} from "../agenthome.service"
@Component({
  selector: 'app-showdetails',
  templateUrl: './showdetails.component.html',
  styleUrls: ['./showdetails.component.css',"../../common.css"]
})
export class ShowdetailsComponent implements OnInit {
  user: any;
agents ;
  fullname: any;
  address: any;
  postcode: any;
  email: any;
  phone: any;
  officephone: any;
  sole: any;
  multi: any;
  uid: any;
  constructor(public AgenthomeService:AgenthomeService) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    if(this.user != null)
    {
      this.uid  = this.user.uid
    }
this.getAgentDetails()
  }
  getAgentDetails()
  {
   this.AgenthomeService.getAgent(this.user.uid).then((newData) => {
     newData.forEach(element => {
      this.agents = element.data()
      this.fullname = this.agents.fullname
      this.address = this.agents.address
      this.postcode = this.agents.postcode
      this.email = this.agents.email
      this.phone = this.agents.phone
      this.officephone = this.agents.officephone
      this.sole = this.agents.solefees
      this.multi = this.agents.multiplefees
     });
   })
 }
}
