import { Component, OnInit } from '@angular/core';
import { CMSSERVICE } from "../../../cms.service"
@Component({
  selector: 'app-mydetailserror',
  templateUrl: './mydetailserror.component.html',
  styleUrls: ['./mydetailserror.component.css','../uielements.component.css', '../../Admin-common.css']
})
export class MydetailserrorComponent implements OnInit {
  isLoading:boolean = false
  MyDetail : any = "MyDetails" 
  MyDetails;
  FullName: any;
  Email: any;
  Phone: any;
  DOB: any;
  ValidateDob: any;
  Address: any;
  Prefrence: any;
  constructor(public CMSSERVICE: CMSSERVICE) { }

  ngOnInit() {
    this.getOtherForm()
  }
  getOtherForm() {
    this.CMSSERVICE.getMyDetailError(this.MyDetail).then((element) => {
     this.FullName = element.data().FullName
     this.Email= element.data().Email
     this.Phone= element.data().Phone
     this.DOB = element.data().DOB
     this.ValidateDob = element.data().ValidateDob
     this.Address  = element.data().Address
     this.Prefrence = element.data().Prefrence
      this.isLoading = false
      
    })

  }

  updateMyDetailError()
  {
    this.isLoading  = true
    this.MyDetails ={
      FullName:this.FullName,
      Email:this.Email,
      Phone:this.Phone,
      DOB:this.DOB,
      ValidateDob:this.ValidateDob,
      Address:this.Address,
      Prefrence:this.Prefrence
    }
    this.CMSSERVICE.updateMyDetailError(this.MyDetail,this.MyDetails)
    this.getOtherForm()
  }

}
