import { Component, OnInit } from '@angular/core';
import { flatMap } from 'rxjs/operators';
import { AgenthomeService } from "./agenthome.service";
import { AuthService } from "../auth.service"
import { AngularFireAuth } from "@angular/fire/auth";
import { HttpService } from "../http.service"
import { StrapiAuthService } from "../strapi-auth.service";
import { first } from 'rxjs/operators'
import { Router, ActivatedRoute } from "@angular/router";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
  MatAutocompleteSelectedEvent,
  MatAutocompleteTrigger,
  MatDialogConfig
} from "@angular/material";

import { Location } from "@angular/common";

import {CMSSERVICE} from "../cms.service"
import { AlertUserTypeComponent } from "../Misc/alert-user-type/alert-user-type.component"
@Component({
  selector: 'app-agenthome',
  templateUrl: './agenthome.component.html',
  styleUrls: ['./agenthome.component.css',"../Misc/CommonCode/cards.css"]
})
export class AgenthomeComponent implements OnInit {
  agents;

  user: any;
  uid: any;
  agentsBuyer = [];
  agentsSeller = [];
  agentNew = [];
  loggedIn: boolean = false
  signUp: any;
  password: any;
  name: any;
  email: any;
  userData: any;
  Companyname: any;
  Address: any;
  Postcode: any;
  Phonenumber: any;
  Jobtitle: any;
  SinglePercentage: any;
  Multipercentage: any;
  id: any;
  Image: any;
  BuyerDetail = [];
  emailField: boolean = false
  notValidEmail: boolean = false
  SignUp: boolean = false
  emailData;
  validity: any;
  Fullname: any;
  PropertyId: any;
  UserId: any;
  UserDetails;
  myLeads:boolean = true
  editprofile:boolean = false
  return: Promise<void>;
  archieved:boolean = false
  archieveUser = []
  type = "Agent"
  hide:boolean = true
  sub: any;
  OtherError: any ="Other"

  isLoading:boolean = false
  AgentSignIn: any;
  UserSignIn: any;
  WrongPassword: any;
 
  constructor(public AgenthomeService: AgenthomeService,
     public authService: AuthService, 
     public afAuth: AngularFireAuth,
      public HttpService: HttpService, 
      public StrapiAuthService: StrapiAuthService, 
      private router: Router, private dialog: MatDialog,    
      public _location: Location, 
      public CMSSERVICE:CMSSERVICE,
      public ActivatedRoute:ActivatedRoute) { }

  ngOnInit() {
    this.getOtherForm()
    this.user = JSON.parse(localStorage.getItem("user"));
    if(this.user != null)
    {
    this.uid = this.user.uid
    this.loggedIn = true
    this.userLogged()
    this.items()
    this.sub = this.ActivatedRoute.paramMap.subscribe((params)=>{
      this.type = params.get("type")
    }) 
    
    

    }


 




  }
 

 

  signIn(email, pass) {

    this.CMSSERVICE.getuserType(email).then((element) => {
      if(element.data() != undefined){
      if(element.data().UserType != "Agent" )
      {
        const dialogRef = this.dialog.open(AlertUserTypeComponent, {
          data: { message: this.AgentSignIn }
        });
      }
      else
      {
        this.authService.SignIn(email, pass).then((data) => {
          this.items()
          this.userLogged()
          window.location.reload()
        });
      }
    }else  {
      this.isLoading = false;
      const dialogRef = this.dialog.open(AlertUserTypeComponent, {
        data: { message: "User Does not exist"}
      });
      }

    });
  
  }
    
  
  validateEmail(email, pass) {

    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (re.test(String(email).toLowerCase()) == false) {
     alert("Enter Valid Email Address")
    }
    else {
      this.signIn(email, pass)
    }
  }

  keyDownFunction(event) {
    if (event.keyCode == 13) {
      this.signUp(this.name, this.email, this.password);
    }
  }


  items() {
    this.BuyerDetail = []
    this.archieveUser = []
   this.AgenthomeService.getAgentSeller(this.uid).then((data)=>{
     data.forEach(element => {
       if(element.data().Archived == false)
       {
        this.BuyerDetail.push({Detail:element.data(),id:element.id})

       }
       else
       {
         this.archieveUser.push({Detail:element.data(),id:element.id})
       }
     });
   })
  }

  userLogged() {
    this.afAuth.authState.subscribe((user) => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        JSON.parse(localStorage.getItem("user"));
        this.loggedIn = true;
        this.agentItem()
        this.checkUrl()
      } else {
        localStorage.setItem("user", null);
        JSON.parse(localStorage.getItem("user"));
        this.loggedIn = false;
      }
    });

  }




  agentItem()
 {
    this.AgenthomeService.getAgent(this.uid).then((newData) => {
      newData.forEach(element => {
        this.Companyname = element.data().company
        this.Address =  element.data().address
        this.Postcode = element.data().postcode
        this.Phonenumber = element.data().phone
        this.Jobtitle = element.data().jobtitle
        this.SinglePercentage = element.data().solefees
        this.Multipercentage = element.data().multiplefees
        this.Image = element.data().image
        this.Fullname = element.data().fullname
        this.PropertyId = element.id
        this.UserId = this.user.uid
      });
    })
  }

myLead()
{
  if(this.loggedIn == true)
  {
    this.myLeads = true
    this.editprofile = false
    this.archieved  = false
      document.getElementById("one").style.background = "white"
      document.getElementById("one").style.border = "none";
      document.getElementById("one").style.borderTop = "3px solid #E8E8E8";
      document.getElementById("two").style.background = "#ECECEC"
      document.getElementById("two").style.border = "1px solid #A4A4A4";
      document.getElementById("three").style.background = "#ECECEC"
      document.getElementById("three").style.border = "1px solid #A4A4A4";

  }
  
}

edit()
{
  this.myLeads = false
  this.editprofile = true
  this.archieved  = false
  this.loggedIn = true
  document.getElementById("two").style.background = "white"
    document.getElementById("two").style.border = "none";
    document.getElementById("two").style.borderTop = "3px solid #E8E8E8";
    document.getElementById("one").style.background = "#ECECEC"
    document.getElementById("one").style.border = "1px solid #A4A4A4";
    document.getElementById("three").style.background = "#ECECEC"
    document.getElementById("three").style.border = "1px solid #A4A4A4";
}


Achieve()
{
  this.archieved  = true
  this.myLeads = false
  this.editprofile = false
  this.loggedIn = true
    document.getElementById("three").style.background = "white"
    document.getElementById("three").style.border = "none";
    document.getElementById("three").style.borderTop = "3px solid #E8E8E8";
    document.getElementById("one").style.background = "#ECECEC"
    document.getElementById("one").style.border = "1px solid #A4A4A4";
    document.getElementById("two").style.background = "#ECECEC"
    document.getElementById("two").style.border = "1px solid #A4A4A4";
}
propertyDetail(property)
{
  localStorage.setItem("sellerPropertyDetail",JSON.stringify(property))
  this.router.navigate(["/selectedAgentbuyerdetail"])
}

sellerDetail(property)
{
  localStorage.setItem("sellerPropertyDetail",JSON.stringify(property))
  this.router.navigate(["/sellerSelectedagent"])
}
buyerDetail(property)
{
  localStorage.setItem("sellerPropertyDetail",JSON.stringify(property))
  this.router.navigate(["/BuyerDetailsAgent"])
}

update(docid,property)
{


  property.Archived = true
this.return = this.AgenthomeService.updateLeads(this.uid,docid,property).then((data)=>{
  this.items()
})


}

restore(docid,property)
{
  property.Archived = false
  this.return = this.AgenthomeService.updateLeads(this.uid,docid,property).then((data)=>{
    this.items()
  })
}

delete(docid)
{
  this.AgenthomeService.deleteProperty(this.uid,docid).then((data)=>{
    this.items()
  })
}

checkUrl()
{
  if(this.type == "leads")
  {
    this.myLead()
  }
  if(this.type == "profile")
  {
    this.edit()
  }
  if(this.type == "archieve")
  {
    this.Achieve()
  }
}

leads()
  {
    this.router.navigate(['/Agenthome/leads'])
    .then(() => {

      window.location.reload();
    });
 
  }
  Profile()
  {
    this.router.navigate(['/Agenthome/profile'])
    .then(() => {
      window.location.reload();
    });
  }
  Archieve()
  {
    this.router.navigate(['/Agenthome/archieve'])
    .then(() => {

      window.location.reload();
    });

  }

  getOtherForm() {
    this.CMSSERVICE.getOtherError(this.OtherError).then((element) => {
     this.AgentSignIn = element.data().AgentSignIn
     this.UserSignIn = element.data().UserSignIn
     this.WrongPassword = element.data().WrongPassword
      this.isLoading = false
      
    })

  }
}
