import { Component, OnInit, Input } from "@angular/core";
import { Observable } from "rxjs";
import { FormsService } from "../../Home/Property/fillFormBuyer/fillFormBuyer.service";
import { listingBuyer } from "../../Model/listingBuyer";
import {HttpService} from '../../http.service'
import { AlertUserTypeComponent } from "../../Misc/alert-user-type/alert-user-type.component";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
} from "@angular/material";
import {AuthService} from "../../auth.service"
@Component({
  selector: "app-profile",
  templateUrl: "./Profile.component.html",
  styleUrls: ["./Profile.component.css", "./../../common.css"]
})
export class MyProfileComponent implements OnInit {
  listingBuyer: listingBuyer;
  user: any;
  items: Observable<any[]>;
  username: any;
  useremail: any;
  users: any;
  Name:any;
  uid:any;
  Address:any
  Newhide:boolean = true
  Conhide:boolean = true
  updatePass:boolean = false
  emailpop:boolean = false
  constructor(private fillFormsService: FormsService,
    private HttpService:HttpService, private dialog: MatDialog,public AuthService:AuthService) {}

  ngOnInit() {
  
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;

    this.fillFormsService.getUser(this.uid).subscribe((ref) => {
      ref.forEach((element) => {
        if(element.data().uid == this.uid)
     {
     this.user.Name = element.data().Name

     this.user.Email = element.data().email
     if( element.data().DOB != null)
     {
      this.user.DOB = element.data().DOB.toDate() 
     }
     if( element.data().Phone != null)
     {
      this.user.Phone = element.data().Phone
     }
     if(element.data().Currentaddress  != null)
     {
      this.user.Currentaddress = element.data().Currentaddress
     }
     if(element.data().prefrence  != null)
     {
      this.user.prefrence= element.data().prefrence
     }
     else
     {
      this.user.prefrence = "Not Selected"
     }
     }
      });
    });


  }
  updatepassword(update,Condirmupdate)
  {
    var patt = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/
    var res = patt.test(Condirmupdate);
    this.updatePass = true
    if(update != Condirmupdate){
    const dialogRef = this.dialog.open(AlertUserTypeComponent, {
      data: { message: "Both Password Does not match" }
    });
    }
  else if (update == Condirmupdate && res == false)
  {
this.emailpop = true
  }
    else
    {
      this.AuthService.UpdatePassword(Condirmupdate)
      const dialogRef = this.dialog.open(AlertUserTypeComponent, {
        data: { message: "Password Updated Successfully" }
      });
      this.updatePass = false
    }
  }
  openupdatePassword()
  {
    this.updatePass = true
  }
  closeUpdatePassword()
  {
    this.updatePass = false
  }
  emailpopclose()
  {
    this.emailpop = false
  }
  
}
