-bar {\n        background-color: white;\n        float: left;\n        height: 92%;\n        box-shadow: 7px 0 5px -2px #00000029;\n        position: fixed;\n        margin-top: 0vh;\n        padding: 0em 1em;\n        width: 8vw;\n        z-index: 1;\n        margin-top: 7vh;\n    }\n    \n}\n/*# sourceMappingURL=data:application/json;base64, */"