import { Component, OnInit } from '@angular/core';
import { MyListingService } from "../myListing.service"
import { Router, ActivatedRoute } from "@angular/router";
import {PageEvent} from '@angular/material/paginator';


@Component({
  selector: 'app-favorates',
  templateUrl: './favorates.component.html',
  styleUrls: ['./favorates.component.css']
})
export class FavoratesComponent implements OnInit {
  getFavorates = []
  user: any;
  uid: any;
  sellerFavorate = []
  sellerProperty: boolean = false
  buyerProperty: boolean = false
  nestimate: string;
  startPage : Number;
  paginationLimit:Number; 
   // MatPaginator Inputs
   length = 100;
   pageSize = 10;
   pageSizeOptions: number[] = [5, 10, 25, 100];
 
   // MatPaginator Output
   pageEvent: PageEvent;
   isLoading:boolean = false

  constructor(public MyListingService: MyListingService,
    private _Activatedroute: ActivatedRoute,
    private _router: Router) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;
    this.getFavorate()
  }
  getFavorate() {
    this.getFavorates = []
    this.sellerFavorate = []
    this.isLoading = true
    this.MyListingService.getFavorate(this.uid).then((data) => {
      data.forEach(element => {
        this.getFavorates.push({ Detail: element.data(), id: element.id })
        this.isLoading = false
      });

      this.MyListingService.sellerFavorates(this.uid).then((data) => {
        data.forEach(element => {
          this.sellerFavorate.push({ Detail: element.data(), id: element.id })
          this.isLoading = false
        })

        if (this.getFavorates.length > this.sellerFavorate.length) {
          this.buyerProperty = true
          this.sellerProperty = false
          this.nestimate = "Owner Nestimate"
          this.isLoading = false
        }
        else {
          this.sellerProperty = true
          this.buyerProperty = false
          this.nestimate = "Seeker Nestimate"
          this.isLoading = false
        }
        this.isLoading = false
        this.startPage = 0;
        this.paginationLimit = 6;
      })
    })
  }


  showMoreItems()
  {
     this.paginationLimit = Number(this.paginationLimit) + 2;        
  }

deleteBuyerFavorate()
{
  this.MyListingService.getFavorate(this.uid).then((data) => {
    data.forEach(element => {
      this.getFavorates.push({ Detail: element.data(), id: element.id })
    });
    alert(this.getFavorates.length)
   if( this.getFavorates.length > 0)
   {
       this.buyerProperty = true
   }
   else
   {
    this.buyerProperty = false

   }
})
}


  delete(docid) {
    this.MyListingService.deletefavorate(this.uid, docid).then(data => {
     this.getFavorate() 
  })


  }

  seeDetailPage(property) {
    localStorage.setItem("SellerListingDetail", JSON.stringify(property));
    this._router.navigate(["/SellerListingDetail"])

  }

  deleteSeller(docid) {
    this.MyListingService.sellerdeletefavorate(this.uid, docid).then(data => {
      this.getFavorate()
    })
  }

  buyerdetailPage(property)
  {
  localStorage.setItem("BuyerListingDetail",JSON.stringify(property))
  this._router.navigate(['/BuyerListingDetail'])
  }
  SeekerNestimate()
  {
    this.sellerProperty = true
    this.buyerProperty = false
  }
  OwnerNestimate()
  {
    this.sellerProperty = false
    this.buyerProperty = true
  }



 //For Seller MatchListing
 unmatchedhightoLow() {
  
  if (this.getFavorates.length > 1) {

    this.getFavorates.sort(function (a, b) {

      return b.Detail.PropertyDetail.MaxAmount.replace(/,/g, "")  - a.Detail.PropertyDetail.MaxAmount.replace(/,/g, "") ;
    });
  }
  
 
}
unmatchedlowtohigh() {
  
  if (this.getFavorates.length > 1) {
    this.getFavorates.sort(function (a, b) {
      return a.Detail.PropertyDetail.MaxAmount.replace(/,/g, "")  - b.Detail.PropertyDetail.MaxAmount.replace(/,/g, "") ;
    });
  }
  

}
unmatchedroomshightolow() {
  
  if (this.getFavorates.length > 1) {
    this.getFavorates.sort(function (a, b) {
      return a.Detail.PropertyDetail.Maxrooms - b.Detail.PropertyDetail.Maxrooms;
    });
  }
  

}
setPageSizeOptions(setPageSizeOptionsInput: string) {
  if (setPageSizeOptionsInput) {
    this.pageSizeOptions = setPageSizeOptionsInput.split(',').map(str => +str);
  }
}
  
//For Seller MatchListing
buyerhightoLow() {
  
  if (this.sellerFavorate.length > 1) {

    this.sellerFavorate.sort(function (a, b) {
      return b.Detail.PropertyDetail.MinAmount.replace(/,/g, "")  - a.Detail.PropertyDetail.MinAmount.replace(/,/g, "") ;
    });
  }
  
 
}
buyerlowtohigh() {
  
  if (this.sellerFavorate.length > 1) {
    this.sellerFavorate.sort(function (a, b) {
      return a.Detail.PropertyDetail.MinAmount.replace(/,/g, "")  - b.Detail.PropertyDetail.MinAmount.replace(/,/g, "") ;
    });
  }
  

}
buyerroomshightolow() {
  
  if (this.sellerFavorate.length > 1) {
    this.sellerFavorate.sort(function (a, b) {
      return a.Detail.PropertyDetail.Roommin - b.Detail.PropertyDetail.Roommin;
    });
  }
  

}

  

}
