import { Component, OnInit } from '@angular/core';
import { AdminPanelService } from "../admin-panel/admin-panel.service.ts.service"
import { map, startWith, take, ignoreElements } from "rxjs/operators";
import { AdminAuthService } from "../admin.auth.service"
import { AltertFormDialogComponent } from "../../Misc/alertFormdialog/alertFormdialog.component";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
} from "@angular/material";
import {FormControl} from '@angular/forms';
import { Router, ActivatedRoute } from "@angular/router";



import { Observable } from 'rxjs';
@Component({
  selector: 'app-users-agent-tab',
  templateUrl: './users-agent-tab.component.html',
  styleUrls: ['./users-agent-tab.component.css', '../Admin-common.css']
})
export class UsersAgentTabComponent implements OnInit {
  getUser: any;
  name: any;
  uid: any;
  image: any;
  allUser = []
  allAgent = 0
  allUserDetails = []
  usersTab: boolean
  tutorials: any;
  addNewUser: boolean = false
  hide: boolean = true
  Type = "User"

  dustbin:boolean = false
  searchEmail;


  myControl = new FormControl();
  options: string[] =[] ;
  filteredOptions: Observable<string[]>;


  agentFormControl = new FormControl();
  agentOptions: string[] = []
  agentfilteredOptions: Observable<string[]>;

  AllAgentJob
  crossSearch:boolean = false


  overLayJobTitle:boolean = false
  jobTitleSearch:boolean = false

  asc=[];
  data: Observable<any>;
  ref;
  sub: any;
  agemtTab: string;
  userTab: string;
  userTabs;
  constructor(public adminPanelService: AdminPanelService,
    public AdminAuthService: AdminAuthService,
    private dialog: MatDialog,
    private _Activatedroute: ActivatedRoute,) { }

  ngOnInit() {

    this.getUser = JSON.parse(localStorage.getItem("user"));
    document.getElementById("users-tab").style.borderBottom = "2px solid #000000"
    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.agemtTab = params.get("agemtTab")
      this.userTabs = params.get("userTab")
      })
      if( this.userTabs == "false")
      {
        this.usersTab = false
        document.getElementById("agent-tab").style.borderBottom = "2px solid #000000"
        document.getElementById("users-tab").style.borderBottom = "none"
      }
      else
      {
        this.usersTab = true
        document.getElementById("agent-tab").style.borderBottom = "none"
        document.getElementById("users-tab").style.borderBottom = "2px solid #000000"
      }
    this.getUserDetails()
    this.getCountUser()
    this.getAllUsers()
    this.retrieveTutorials();
   
  
  
  }












deleteMultiple(value)
{
  this.adminPanelService.multipledeleteUser(value)
}


  getValue(value)
  {
    this.dustbin = true
    this.deleteMultiple(value)

  }

  showJobTiltle(item)
  {
    this.jobTitleSearch = true
    this.overLayJobTitle = false
    this.tutorials =[]
    this.adminPanelService.getAllAgent().snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ id: c.payload.doc.id, ...c.payload.doc.data() })
        )
      )
    ).subscribe(data => {
      this.AllAgentJob = data
      data.forEach((element)=>{
        if(element.jobtitle ==  item)
      {
        this.tutorials.push(element)
      }
      })
    });
  }

overLayJob()
{
 
this.overLayJobTitle = true
}
closeroverLayJob()
{
 
this.overLayJobTitle = false
}


removeAgentSearch()
{
  this.searchEmail = null
  this.crossSearch = false
  this.retrieveTutorials()
  this.getCountUser() 
}

removeUserSearch()
{
  this.searchEmail = null
  this.crossSearch = false
  this.retrieveTutorials()
  this.getCountUser()
}
  
  getGentSearchValue()
  {
    this.crossSearch = true
    this.tutorials =[]
    this.allAgent = 0


      this.adminPanelService.getAllAgent().snapshotChanges().pipe(
        map(changes =>
          changes.map(c =>
            ({ id: c.payload.doc.id, ...c.payload.doc.data() })
          )
        )
      ).subscribe(agent => { this.allAgent = agent.length});
  
    this.adminPanelService.getAllAgent().snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ id: c.payload.doc.id, ...c.payload.doc.data() })
        )
      )
    ).subscribe(data => {
      data.forEach((element)=>{
        console.log(element.email.toLowerCase().includes(this.searchEmail.toLowerCase()))
        if(element.email.toLowerCase().includes(this.searchEmail.toLowerCase()) ||
         element.fullname.toLowerCase().includes(this.searchEmail.toLowerCase()) || 
         element.postcode.toLowerCase().includes(this.searchEmail.toLowerCase()))
      {
        this.tutorials.push(element)
      }
      })
    });
    
  }



  getSearchValue()
  {
    this.crossSearch = true
    this.allUser  = []
    this.adminPanelService.getAllUser().then((element) => {
      element.forEach(elements => {
        if (elements.data().UserType == "User") {
          if( elements.data().email.toLowerCase().includes(this.searchEmail.toLowerCase()) ||  elements.data().Name.toLowerCase().includes(this.searchEmail.toLowerCase()))
        {
          this.allUser.push({ detail: elements.data(), uid: elements.data().uid })
        }
        }
      });

    })
  }

  agentascDesc()
  {
    this.tutorials.sort(function (a,b) {
      return 0 - (a.fullname > b.fullname  ? -1 : 1);
    });
  }
ascDesc()
{
  this.allUser.sort(function (a,b) {
    return 0 - (a.detail.uid > b.detail.Name  ? -1 : 1);
  });
}

deleteUser(uid)
{
  this.adminPanelService.deleteUser(uid).then((data)=>{
    const dialogRef = this.dialog.open(AltertFormDialogComponent, {
      data: { message: "User Deleted" }
    });
  })
  this.allUser =[]
  this.getCountUser()
}



deleteAgent(uid)
{
  this.adminPanelService.deleteAgent(uid).then((data)=>{
  this.deleteUser(uid)
  })

}


  addUserPopup() {
    this.addNewUser = true
  }
  addUserPopupClose() {
    this.addNewUser = false
  }


  getUserDetails() {

    if (this.getUser != null) {
      this.uid = this.getUser.uid
      this.adminPanelService.getUser(this.uid).then((element) => {
        this.name = element.data().Name
        this.image = element.data().Image
      })

    }
  }


  getCountUser() {
    this.allUser = []
    this.allAgent = 0
    this.crossSearch = false
    this.adminPanelService.getAllAgent().snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ id: c.payload.doc.id, ...c.payload.doc.data() })
        )
      )
    ).subscribe(agent => { this.allAgent = agent.length});
    this.adminPanelService.getAllUser().then((data) => {
      data.forEach(elements => {
        if (elements.data().UserType != "Agent" && elements.data().UserType != "Admin") {
          this.allUser.push({ detail: elements.data(), uid: elements.data().uid })
          this.options.push(elements.data().email)
        }

      });

    })
  }


  getAllUsers() {
    this.adminPanelService.getAllUser().then((element) => {
      element.forEach(elements => {
        this.allUserDetails.push(elements.data())
      });

    })
  }


  retrieveTutorials(): void {
    this.jobTitleSearch = false
    this.adminPanelService.getAllAgent().snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ id: c.payload.doc.id, ...c.payload.doc.data() })
        )
      )
    ).subscribe(data => {
      this.tutorials = data;
      this.AllAgentJob = data
      data.forEach((element)=>{
        this.agentOptions.push(element.email)
      })
    });
  }
  activeUserAgentTab() {
    this.usersTab = false
    document.getElementById("agent-tab").style.borderBottom = "2px solid #000000"
    document.getElementById("users-tab").style.borderBottom = "none"
  }
  activeUserTab() {
    this.usersTab = true
    document.getElementById("agent-tab").style.borderBottom = "none"
    document.getElementById("users-tab").style.borderBottom = "2px solid #000000"
  }
  createUser(Name, email, password, Type) {
    console.log(Name, email, password, Type)
    this.AdminAuthService.adminSignUp(Name, email, password, Type).then((data) => {
      const dialogRef = this.dialog.open(AltertFormDialogComponent, {
        data: { message: "Account Created and Verification Email send" }
      });
    })
  }
}
