
import { Injectable, EventEmitter } from "@angular/core";
import {
  AngularFirestore,
  AngularFirestoreCollection
} from "@angular/fire/firestore";
import {
  AngularFirestoreDocument
} from "@angular/fire/firestore";
import { Observable, onErrorResumeNext } from "rxjs";
import { listingSeller } from "../../Model/listingSeller"
import { map, startWith, take, ignoreElements } from "rxjs/operators";
import { agentSignup } from "../../Model/agentSignup";
import { listingBuyer } from "../../Model/listingBuyer";
import { expressMatches } from "../../Model/matchesExpress"
import { expressInterest } from "../../Model/expressInterest"
import * as firebase from 'firebase';

import {matchesBuyer} from "../../Model/matchesBuyer"
import {matchesSeller} from "../../Model/matchesSeller"



@Injectable({
  providedIn: 'root'
})
export class AdminPanelService {
  userCollection: any;
  buyer: any;
  seller: any;
  sellerUser: any
  Agents: any
  agentRef: any
  customersRef: AngularFirestoreCollection<listingSeller> = null;
  agentPrperties: any
  buyerListing: any
  sellerListing: any;
  expressMatches: any
  expressInterest: any;
  MatchesBuyer:any;
  private dbPath = "listingSeller";
  public myEventList: Observable<any>;


  
  constructor(private db: AngularFirestore,
  ) {


    this.userCollection = db.collection("users")

    this.buyer = db.collection("listingBuyer")
    this.seller = db.collection("listingSeller")
    this.sellerUser = db.collectionGroup("properties");
    this.customersRef = db.collection(this.dbPath);
    this.Agents = db.collectionGroup("agents")

    this.agentRef = db.collection("agentSignup")
    this.agentPrperties = db.collection("agentSeller")
    this.buyerListing = db.collectionGroup("requirements")
    this.sellerListing = db.collectionGroup("properties")

    this.expressMatches = db.collectionGroup("MatchesExpress")
    this.expressInterest = db.collectionGroup("Express")

    this.MatchesBuyer=db.collectionGroup("matches")

  }



  getBuyerMatchesInterest(): AngularFirestoreCollection<matchesBuyer> {
    return   this.MatchesBuyer
  }

  getSellerMatchesInterest(): AngularFirestoreCollection<matchesSeller> {
    return   this.MatchesBuyer
  }


  deleteSellerConFirmInterestBuyer(uid ,prop){

    return this.db
      .collection("matchesSeller")
      .doc(uid)
      .collection("matches")
      .doc(prop)
      .delete()
  
  }

  deleteConFirmInterestBuyer(uid,propid){
    return this.db
      .collection("matchesBuyer")
      .doc(uid)
      .collection("matches")
      .doc(propid)
      .delete()
}

getSellerConFirmInterestBuyer(uid){

  return this.db
    .collection("matchesSeller")
    .doc(uid)
    .collection("matches")
    .ref.get()

}



  getConFirmInterestBuyer(uid){

      return this.db
        .collection("matchesBuyer")
        .doc(uid)
        .collection("matches")
        .ref.get()
  
  }
 
  deleteImageSeller(uid, docid) {
    return this.db
      .collection("listingSeller")
      .doc(uid)
      .collection("properties")
      .doc(docid)
      .set({Images: firebase.firestore.FieldValue.delete() }
      ,{ merge: true }
      )
  }

  multipledeleteUser(uid) {
    return this.db
      .collection("users")
      .doc(uid)
      .delete();
  }

  

  getcountExpressInterest(): AngularFirestoreCollection<expressInterest> {
    return this.expressInterest
  }

  getcountExpressMatches(): AngularFirestoreCollection<expressMatches> {
    return this.expressMatches
  }




  getAllCountSeller(): AngularFirestoreCollection<listingSeller> {
    return this.sellerListing;
  }
  getAllBuyer(): AngularFirestoreCollection<listingBuyer> {
    return this.buyerListing;
  }







  //Create Customer ListingBuyer
  async updateAgentListing(uid, docid, agents) {
    return this.db
      .collection("agentSignup")
      .doc(uid)
      .collection("agents")
      .doc(docid)
      .set(Object.assign({}, agents))
      .then(function (data) {
        console.log("agent update!");
      });
    return true;
  }


  deleteBlog(uid,propertyId) {
    return this.db
      .collection("Blogs")
      .doc(uid)
      .collection("AllBlogs")
      .doc(propertyId)
      .delete();

  }

  deleteAgent(uid) {
    return this.db
      .collection("agentSignup")
      .doc(uid)
      .delete();

  }

  deleteUser(uid) {
    return this.db
      .collection("users")
      .doc(uid)
      .delete();
  }



  deleteBuyer(uid, docid) {
    return this.db
      .collection("listingBuyer")
      .doc(uid)
      .collection("requirements")
      .doc(docid)
      .delete();
  }


  deleteSeller(uid, docid) {
    return this.db
      .collection("listingSeller")
      .doc(uid)
      .collection("properties")
      .doc(docid)
      .delete();
  }
  getUser(uid): any {
    return this.userCollection
      .doc(uid)
      .ref.get();
  }

  getAllAgent(): AngularFirestoreCollection<agentSignup> {
    return this.Agents;
  }



  getAllUser(): any {
    return this.userCollection.ref.get();
  }


  getSelectedAgent(uid, propertyId) {
    return this.agentRef
      .doc(uid)
      .collection("agents")
      .ref.get()
  }

  getagentProperties(uid) {
    return this.agentPrperties
      .doc(uid)
      .collection("PropertyDetails")
      .ref.get()
  }

  getselectedagentProperties(uid, propId) {
    return this.agentPrperties
      .doc(uid)
      .collection("PropertyDetails")
      .doc(propId)
      .ref.get()
  }





  getBuyerEntries(uid): any {
    return this.buyer
      .doc(uid)
      .collection("requirements")
      .ref.get()
  }


  getSellerEntries(uid): any {
    return this.seller
      .doc(uid)
      .collection("properties")
      .ref.get()
  }

  getAllSeller() {
    return this.seller
      .collection("properties")
      .ref.get()
  }
  getCustomersList(): AngularFirestoreCollection<listingSeller> {
    return this.customersRef
  }





  userupate(user) {
    const userRef: AngularFirestoreDocument<any> = this.db.doc(`users/` + user.uid)
    const userData: any = {
      uid: user.uid,
      email: user.email,
      Name: user.Name,
      DOB: user.DOB,
      Phone: user.Phone,
      title: user.title,
      Currentaddress: user.Currentaddress,
      Currentpostcode: user.Currentpostcode,
      Currentstate: user.Currentstate,
      CurrentTowncity: user.CurrentTowncity,
    };
    return userRef.set(userData, {
      merge: true,

    });

  }



  getBuyerPrpertyDetails(uid, propertyId) {
    return this.buyer
      .doc(uid)
      .collection("requirements")
      .doc(propertyId)
      .ref.get()
  }



  //Create Customer ListingBuyer
  async updateBuyerListing(uid, docid, listingBuyer) {
    return this.db
      .collection("listingBuyer")
      .doc(uid)
      .collection("requirements")
      .doc(docid)
      .set(Object.assign({}, listingBuyer))
      .then(function (data) {
        console.log("Document successfully written!");
      });
    return true;
  }



  //Get Seller Selected
  getSellerPrpertyDetails(uid, propertyId) {
    return this.seller
      .doc(uid)
      .collection("properties")
      .doc(propertyId)
      .ref.get()
  }

  //Create Customer ListingBuyer
  async updateSellerListing(uid, docid, listingSeller) {
    return this.db
      .collection("listingSeller")
      .doc(uid)
      .collection("properties")
      .doc(docid)
      .set(Object.assign({}, listingSeller))
      .then(function (data) {
        console.log("Document successfully written!");
      });
    return true;
  }

  usersellerupate(user) {
    const userRef: AngularFirestoreDocument<any> = this.db.doc(`users/` + user.uid)
    const userData: any = {
      Currentaddress: user.Currentaddress,
      Currentpostcode: user.Currentpostcode,
      Currentstate: user.Currentstate,
      CurrentTowncity: user.CurrentTowncity,
      Currentcountry: user.Currentcountry
    };
    return userRef.set(userData, {
      merge: true,

    });

  }

}
