import { Component, OnInit } from '@angular/core';
import { MyListingService } from "../myListing.service"
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-recentlyitems',
  templateUrl: './recentlyitems.component.html',
  styleUrls: ['./recentlyitems.component.css']
})
export class RecentlyitemsComponent implements OnInit {
  user: any;
  uid: any;
  recentItems = []
  sellerProperty: boolean = false
  buyerProperty: boolean = false
  sellerrecentItems = []
  nestimate: string;
  now:Date = new Date();
  flatarray = []
  item=[]
  startPage : Number;
  paginationLimit:Number; 
  isLoading:boolean = false

 
  constructor(public MyListingService: MyListingService, private _router: Router) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;

    this.getAllListing()

  }



  getAllListing() {
    this.recentItems = []
    this.sellerrecentItems = []
    this.isLoading = false
    this.MyListingService.getRecentlyItems(this.uid).then((data) => {
      data.forEach(element => {
        // console.log(element.data().time * 1000)


        this.recentItems.push({ Detail: element.data(), id: element.id })
        this.isLoading = false
      });

      this.MyListingService.getSellerRecentlyItems(this.uid).then((data) => {
        data.forEach(element => {
          this.sellerrecentItems.push({ Detail: element.data(), id: element.id })
          this.isLoading = false
        });

       if(this.recentItems.length > this.sellerrecentItems.length)
       {
    
        this.sellerProperty = true
        this.buyerProperty = false
        this.nestimate = "Seeker Nestimate"
      }
      else {
        this.buyerProperty = true
        this.sellerProperty = false
        this.nestimate = "Owner Nestimate"
     
      }
      this.isLoading = false
      this.startPage = 0;
      this.paginationLimit = 6;
      })

    })
  }
  showMoreItems()
  {
     this.paginationLimit = Number(this.paginationLimit) + 2;        
  }


  seeDetailPage(property) {
    localStorage.setItem("SellerListingDetail", JSON.stringify(property));
    this._router.navigate(["/Sellerrecentproperty"])

  }
  buyerdetailPage(property) {
    localStorage.setItem("BuyerListingDetail", JSON.stringify(property))
    this._router.navigate(['/Buyerrecentlydetail'])
  }
  SeekerNestimate() {
    this.sellerProperty = true
    this.buyerProperty = false
  }
  OwnerNestimate() {
    this.sellerProperty = false
    this.buyerProperty = true
  }

  deleteSeller(docid) {
    this.MyListingService.recentlySellerDelete(this.uid, docid).then(data => {
      this.getAllListing()
    })
  }

  delete(docid) {
    this.MyListingService.recentlyBuyerDelete(this.uid, docid).then(data => {
      this.getAllListing()
    })
  }

//For Seller MatchListing
unmatchedhightoLow() {
  
  if (this.recentItems.length > 1) {

    this.recentItems.sort(function (a, b) {

      return b.Detail.MaxAmount.replace(/,/g, "")  - a.Detail.MaxAmount.replace(/,/g, "") ;
    });
  }
  
 
}
unmatchedlowtohigh() {
  
  if (this.recentItems.length > 1) {
    this.recentItems.sort(function (a, b) {
      return a.Detail.MaxAmount.replace(/,/g, "")  - b.Detail.MaxAmount.replace(/,/g, "") ;
    });
  }
  

}
unmatchedroomshightolow() {
  
  if (this.recentItems.length > 1) {
    this.recentItems.sort(function (a, b) {
      return a.Detail.Maxrooms - b.Detail.Maxrooms;
    });
  }
  

}

  
//For Seller MatchListing
buyerhightoLow() {
  
  if (this.sellerrecentItems.length > 1) {

    this.sellerrecentItems.sort(function (a, b) {

      return b.Detail.MinAmount.replace(/,/g, "")  - a.Detail.MinAmount.replace(/,/g, "") ;
    });
  }
  
 
}
buyerlowtohigh() {
  
  if (this.sellerrecentItems.length > 1) {
    this.sellerrecentItems.sort(function (a, b) {
      return a.Detail.MinAmount.replace(/,/g, "")  - b.Detail.MinAmount.replace(/,/g, "") ;
    });
  }
  

}
buyerroomshightolow() {
  
  if (this.sellerrecentItems.length > 1) {
    this.sellerrecentItems.sort(function (a, b) {
      return a.Detail.Roommin - b.Detail.Roommin;
    });
  }
  

}



}
