import { Component, OnInit } from '@angular/core';
import { SelectAgentService } from './select-agent.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { notification } from "../../../Model/notification";
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import { SelectedMyMatchesService } from "../my-matches-selected-details/selected-my-matches.service";
import { agentBuyer } from "../../../Model/agentBuyer";
import { HttpService } from "../../../http.service"
import { HttpClient, HttpHeaders } from "@angular/common/http";
import {AgentSubmissionComponent} from "./agentSubmission.component";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
} from "@angular/material";
import { NavigationEnd } from '@angular/router';
@Component({
  selector: 'app-select-agent',
  templateUrl: './select-agent.component.html',
  styleUrls: ['./select-agent.component.css',"../../../Misc/CommonCode/cards.css"]
})
export class SelectAgentComponent implements OnInit {
  color = 'accent';
  checked = true;
  disabled = false;
  accepted: boolean = false
  user: any;
  uid: any;
  disable: boolean = true
  agents = [];
  notification: notification;
  now: Date = new Date();
  return: any
  propertyId: any;
  sub: any;
  UserId: any;
  matchesSeller: any;
  Lookingpostcode: any;
  Lookingstate: any;
  LookingAddress: any;
  norooms: any;
  PropertyCondition: any;
  MinAmount: any;
  PropertyType: any;
  ownership: any;
  features: any;
  matchStatus: any;
  MaxAmount: any;
  expressed: any;
  userId: any;
  sellerProperty = [];
  matchesBuyer: any;
  seller_Selected_propertydetail_Service: any;
  isSellerSelected: boolean;
  datastored: boolean;
  express: boolean;
  Amount: any;
  sellerPropertyLookingAddress: string;
  sellerPropertyLookingTown: any;
  sellerPropertyLookingpostcode: any;
  sellerPropertyLookingstate: any;
  sellerPropertyMaxAmount: any;
  sellerPropertyMaxbathrooms: any;
  Maxreception: any;
  sellerPropertyMaxrooms;
  sellerPropertyPropertyCondition: any;
  sellerPropertyPropertyType: any;
  sellerPropertyUserId: any;
  sellerPropertyfeatures: any;
  sellerPropertyownership: any;
  testagents = [];
  agentBuyer: any;
  ChainStatus: string;
  Conditions: string;

  Type: any;
  agentSeller: any
  sellerPropertyPropertyfor: any;
  newItem: any;
  newItem1: any;
  id: string;
  buyerDetail: any;
  sellerPropertyMaxreception: any;
  buyerId: string;
  buyerDetails;
  sellerDetails;
  sellerPropertyId: string;
  sellerId: string;
  sellerCurrentPropertyId: string;
  PropertyFor: string;
  BuyerUserId: string;
  BuyerPropertyId: string;
  SellerPropertyId: string;
  BuyerDetails;
  details;
  userDetail: any;
  SellerUSerId: any;

  constructor(public AgentService: SelectAgentService, private _Activatedroute: ActivatedRoute,
    private _location: Location, private _router: Router, private SelectedMyMatchesService: SelectedMyMatchesService,
    public HttpService: HttpService, public HttpClient: HttpClient,    private dialog: MatDialog,
    ) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    if (this.user != null) {
      this.uid = this.user.uid;
    this.getDetailUser()
    }
    this._router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
    });
    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.propertyId = params.get("propertyId")
    });

    
 

  }
  Acceptterms() {
    this.accepted = true;
    this.disable = false
  }
  uncheckterms() {
    this.accepted = false;
    this.disable = true
  }
  addToExpressCollection() {
    this.return = this.AgentService.ExpressInterest(
      this.uid,
      this.propertyId
    ).then(data => { })

  }

  backClicked() {
    this._location.back();
  }

  createSellerNotification() {
    this.notification = {
      time: this.now,
      viewed: "Confirmed",
      userId: this.uid,
      Type: "Agent_Matches_Confirmed",
      propertyId: this.propertyId,
      Lastseen: null
    }
    this.return = this.AgentService
      .createNotification(this.BuyerUserId, this.notification)
      .then(data => {
        
      });
  }


  createBuyerEntry(agentuid) {
  
    this.createSellerNotification()
    this.createAgentNotification(agentuid)
    this.addToExpressCollection()
    this.agentDetailEntry(agentuid)


    const dialogRef = this.dialog.open(AgentSubmissionComponent, {
      data: { message: "Postcode must be atleast 5 characters long" },
    });

  }

  agentDetailEntry(agentuid) {
    this.AgentService.getDellerDetails(this.uid, this.SellerPropertyId).then((rest) => {
      this.sellerDetails = rest.data()
      this.AgentService.getBuyerrDetails(this.BuyerUserId, this.BuyerPropertyId).then((rest) => {
        this.BuyerDetails = rest.data()
        this.details = {
          SellerDetails: this.sellerDetails,
          BuyerDetails: this.BuyerDetails,
          UserId: agentuid,
          Archived:false
        }
        this.return = this.AgentService.matchesAgentSellerCreate(agentuid, this.details).then(data => { });
      });
    });
    this.BuyerDetails = {
      SellerDetails: this.sellerDetails,
      BuyerDetails: this.BuyerDetails,
      BuyerUserId: this.BuyerUserId,
      BuyerPropertyId: this.BuyerPropertyId,
      SellerPropetyId: this.SellerPropertyId,
      SellerUserId: this.uid,
      matchStatus: "confirmed"
    }
    this.return = this.AgentService.matchesBuyerCreate(this.BuyerUserId, this.BuyerDetails).then(data => { });
  }

  createAgentNotification(agentuid) {
    this.notification = {
      time: this.now,
      viewed: "Confirmed",
      userId: this.uid,
      Type: "Seller_Select_Agent",
      propertyId: this.propertyId,
      Lastseen: null
    }
    this.return = this.AgentService
      .createNotification(agentuid, this.notification)
      .then(data => {

      });

  }

  getDetailUser() {
    const details = JSON.parse(localStorage.getItem("matchesSeller"))
    this.userDetail = details.detail
    this.BuyerPropertyId = this.userDetail.BuyerPropertyId 
    this.BuyerUserId = this.userDetail.BuyerUserId
    this.SellerPropertyId = this.userDetail.SellerPropertyId 
    this.SellerUSerId = this.userDetail.SellerUserId
    this.Lookingpostcode = this.userDetail.BuyerDetails.Lookingpostcode
    this.getSellerDetails()
    this.getBuyerDetails()


    this.AgentService.getAgent().subscribe((ref) => {
      ref.forEach(elements => {
        // this.agents.push({ Detail: elements.data(), uid: elements.data().uid })
        // this.testagents.push(elements.data().uid)
        if (elements.data().postcode.substring(0, 3) == this.Lookingpostcode.substring(0, 3)) {
          this.agents.push({ Detail: elements.data(), uid: elements.data().uid })
          this.testagents.push(elements.data().uid)
        }

      })

    })

  }



  getSellerDetails() {

    this.AgentService.getDellerDetails(this.SellerUSerId, this.SellerPropertyId).then((rest) => {
      this.sellerDetails = rest.data()
     

    });

  }
  getBuyerDetails() {
    this.AgentService.getBuyerrDetails(this.BuyerUserId, this.BuyerPropertyId).then((rest) => {
      this.BuyerDetails = rest.data()
    });

  }



}
