import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {SelectedMyMatchesService} from  "../my-matches-selected-details/selected-my-matches.service";
import { Location } from "@angular/common";
import { NavigationEnd } from '@angular/router';
import { SellerNestimateDetails } from "../../myListings/listing-selling-property-detail/seller-nestimate-details.service"
@Component({
  selector: 'app-buyerAwaitingDetail',
  templateUrl: './buyerAwaitingDetail.component.html',
  styleUrls: ['./buyerAwaitingDetail.component.css',"../../../Misc/CommonCode/seeDetails.css" ,"../../../Misc/CommonCode/Photosseller.css"]
})
export class BuyerAwaitingDetailComponent implements OnInit {
  Lookingpostcode: any;
  Lookingstate: any;
  LookingAddress: any;
  norooms: any;
  PropertyCondition: any;
  MinAmount: any;
  sub: any;
  PropertyType: string;
  ownership: string;
  features: string;
  matchStatus:any;
  MaxAmount: string;
  propertyId: any;
  return: any;
  uid: any;
  user: any;
  expressed: string;
  SellerpropertyId: string;
  SellerUserId: string;
  BuyerUserId: string;
  BuyerPropertyId: string;
  SellerPropertyId: string;
  SellerUSerId: string;


  userDetails:any
  PropertyFor: any;
  Maxrooms: any;
  streetName: any;
  allImages =[]
  ImagesOpen:boolean = false
  SellerUserid: any;
  sellerPropId: any;
  constructor( private _Activatedroute: ActivatedRoute,
    private _router: Router,private SelectedMyMatchesService : SelectedMyMatchesService,
    private _location: Location,
    public SellerNestimateDetails:SellerNestimateDetails) { }

  ngOnInit() {
    this._router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
    });
    
    this.user = JSON.parse(localStorage.getItem("user"));
    if(this.user != null)
    {
    this.uid = this.user.uid;
    }
  
    this.userDetails = JSON.parse(localStorage.getItem("awaitingResponse"))
console.log(this.userDetails)
    this.MaxAmount = this.userDetails.SellerDetails.MaxAmount
    this.PropertyFor =  this.userDetails.SellerDetails.PropertyFor
    this.Lookingpostcode = this.userDetails.SellerDetails.Lookingpostcode
    this.PropertyType = this.userDetails.SellerDetails.PropertyType
    this.PropertyFor = this.userDetails.SellerDetails.PropertyFor
    this.PropertyCondition = this.userDetails.SellerDetails.PropertyCondition
   this.Maxrooms = this.userDetails.SellerDetails.Maxrooms
   this.streetName  = this.userDetails.SellerDetails.Lookingstate
   this.ownership = this.userDetails.SellerDetails.ownership
   this.allImages = this.userDetails.SellerDetails.Images
   this.SellerUserid = this.userDetails.SellerUSerId
   this.sellerPropId = this.userDetails.SellerPropertyId
  }
  backClicked() {
    localStorage.removeItem("awaitingResponse");

    this._router.navigate(["/myListing/myNestimate/waiting"]).then(()=>{
      window.location.reload();
    })
  }

  getAllSeller()
  {
    this.SellerNestimateDetails.getSellerRequirement(this.uid, this.propertyId).then(res => {
    
     this.PropertyCondition = res.data().PropertyCondition
    this.allImages = res.data().Images


    })
    //this.allImages = res.data().Images
  }

  OpenImage()
  {
    this.ImagesOpen = true
  }
  closeImage()
  {
    this.ImagesOpen = false
  }
}
