import { Component, OnInit } from "@angular/core";
import { MyMatchesService } from "../myMatches.service";

import { Router, ActivatedRoute } from "@angular/router";


@Component({
  selector: 'app-awaitingresponse',
  templateUrl: './awaitingresponse.component.html',
  styleUrls: ['./awaitingresponse.component.css', "../../../Misc/CommonCode/cards.css"]
})
export class AwaitingresponseComponent implements OnInit {
  uid: any;
  user: any;
  propertyDetails = [];
  propertyBuyer = [];
  matchedProperties: any;
  property: any;
  UserId: any;
  sub: any;
  Lookingpostcode: string;
  LookingAddress: string;
  norooms: string;
  PropertyCondition: string;
  MinAmount: string;
  buyerProperty: any = [];
  sellerProperty: any = [];
  return: Promise<void>;
  distinctExpressedUid = [];
  expressed: any = false;
  propertyId: any;
  propertyLength: any;
  sellerLength: any;
  elementId;
  buyerProperties;
  elementIds: any;
  alreadyExpressed: any;
  totalBuyer: any;
  totalseller: any;
  buyerLength: any;
  isLoading:boolean = true
  constructor(
    private MatchesService: MyMatchesService,
    private _Activatedroute: ActivatedRoute,
    private _router: Router,

  ) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    if (this.user != null) {
      this.uid = this.user.uid;
    }
    // Fetch details Seller
    this.MatchesService.getMatchesSellerProperties(this.uid).then((res) => {
      res.forEach((element) => {

        if (element.data().matchStatus == "pending") {
          this.sellerProperty.push({ detail: element.data(), propertyId: element.id });
        }
      });
    });
    // Fetch details Seller
    this.MatchesService.getMatchesBuyerProperties(this.uid).then((res) => {
      res.forEach((element) => {



        if (element.data().matchStatus == "pending") {
          this.buyerProperty.push({ detail: element.data(), propertyId: element.id });
        }

       
      });
      this.isLoading = false
    });
  }


  getExpressedListingIds() {
    this.MatchesService.ExpressInterest(this.uid).subscribe((ref) => {
      ref.forEach((item) => {
        if (this.distinctExpressedUid.indexOf(item.data().propertyId) === -1) {
          this.distinctExpressedUid.push(item.data().propertyId);
        }
      });
    });
  }


  checkExpressForValue(uid) {
    if (this.distinctExpressedUid.includes(uid)) {
      this.expressed = true
      return true;
    } else {
      this.expressed = false
      false;
    }
  }


  printvlaue(expressed,property) {

    localStorage.setItem("awaitingResponse", JSON.stringify(property));
    this._router.navigate(["/awaitingResponse/" + expressed])
  }

sellerMatches(property)
{
  localStorage.setItem("MyMatchesseller", JSON.stringify(property));
    this._router.navigate(["/MyMatchesseller/"])
}


}