import { Component, OnInit } from "@angular/core";
import { AuthService } from ".././auth.service";
import { AngularFireAuth } from "@angular/fire/auth";
import { Router, ActivatedRoute } from "@angular/router";
import { FormsService } from "../Home/Property/fillFormBuyer/fillFormBuyer.service";
import { HttpClient, HttpHeaders } from "@angular/common/http";


import { AlertLoginComponent } from "./../Misc/alert-login/alert-login.component";
import { AlertUserTypeComponent } from "./../Misc/alert-user-type/alert-user-type.component"
import { Location } from "@angular/common";

import { auth } from "firebase/app";
import {
  AngularFirestore,
  AngularFirestoreDocument
} from "@angular/fire/firestore";

import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA
} from "@angular/material";

import { CMSSERVICE } from "../cms.service"

@Component({
  selector: "app-template",
  templateUrl: "./template.component.html",
  styleUrls: ["./template.component.css"],
})
export class TemplateComponent implements OnInit {
  uid: any;
  selectedIndex = 0;
  maxNumberOfTabs = 2;
  isLoggedIn: Boolean = true;
  userData: any;
  boolean: boolean;
  isLoading: boolean = false;
  newUser: boolean = false;
  return: any;
  user: any;
  useremail: any;
  DOB: string;
  age: any;
  name: any;
  userPasswordRegister: any;
  model: any = {};
  loading = false;
  returnUrl: string;
  overlay: boolean = false;
  email: any;
  pass: any;
  userEmailRegister: any;
  password;
  type: any = "User"
  emails: any;
  passs: any;
  now: Date = new Date();
  getUser: any;
  notificationOverlay: Object;
  notificationContent: any;
  emailField: boolean = false
  notValidEmail: boolean = false
  SignUp: boolean = false
  emailData;
  topLine: any;
  secondLine: any;
  validity: any;
  hide = true;
  sub: any;
  KnowUser = [];
  getKnownUser;

  fullPop:boolean = true

  constructor(
    public afs: AngularFirestore, // Inject Firestore service
    public afAuth: AngularFireAuth, // Inject Firebase auth service
    public router: Router,
    public authService: AuthService,
    private FormService: FormsService,
    public HttpClient: HttpClient,
    public _location: Location,
    private dialog: MatDialog,
    private activatedRoute: ActivatedRoute,
    public CMSSERVICE: CMSSERVICE
  ) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    console.log(this.user)
    this.afAuth.authState.subscribe((user) => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        JSON.parse(localStorage.getItem("user"));
        this.LoggedOut();
      }
    });
    if( this.passs != null){
      this.emails = localStorage.getItem("email")
      this.passs = localStorage.getItem("password")
    }
   


    this.getNotificationContent()
    this.getSignupTerms()

  }
  private LoggedIn() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }
  alreadyUser() {
    this.SignUp = false
  }
  facebookLogin() {
    this.isLoading = true;

    this.authService.FacebookAuth().then((data) => {
      this.isLoading = false;
    });
  }

  //SignIn Google
  googleLogin() {
    this.isLoading = true;
    this.authService.GoogleAuth().then((data) => {
      this.isLoading = false;
    });
  }

  //Signup Google
  googleSignup() {
    this.isLoading = true;
    this.authService.GoogleAuthSignup().then((data) => {
      this.isLoading = false;
    });
  }

  signIn(email, pass) {
    this.isLoading = true;
    this.CMSSERVICE.CheckUser(email, this.type).then((data) => {
      data.forEach(element => {
        this.KnowUser.push(element.data())
        if (element.data().email == email && element.data().UserType != "User") {
          const dialogRef = this.dialog.open(AlertUserTypeComponent, {
            data: { message: "Please Login as User" }
          });
          this.isLoading = false;
        }
        else if (element.data().email == email && element.data().UserType == "User") {
          console.log(element.data())
          this.authService.SignIn(email, pass).then((data) => {
            this.isLoading = false;
            window.location.reload()
          });
        }
      });
    })
  }

  NewUser() {
    this.newUser = true;
  }

  OldUser() {
    this.newUser = false;
  }
  close() {
    this.newUser = false;
    this.newUser = false;
  }

  async  signUp(displayName, email, pass, User) {
    this.isLoading = true;
    try {
      const result = await this.afAuth.auth.createUserWithEmailAndPassword(email,pass);
     this.authService.SetUserDatas(result.user,displayName,User);
     this.isLoading = false;
     this.isLoggedIn = true;
     this.overlay = true
    } 
    catch (error) {
      window.alert(error.message);
      this.isLoading = false;
    }
  }


  continueClose() {
    this.overlay = false;
    window.location.reload()
  }

  // keyDownFunction(event) {
  //   if (event.keyCode == 13) {
  //     this.signUp(this.name, this.email, this.password);
  //   }
  // }

  saves() {

    this.signIn(this.emails, this.passs);

  }
  newCustomeer() {
    this.SignUp = true

  }




  getNotificationContent() {
    this.CMSSERVICE.getBlueNotification(this.uid).then((res) => {

      this.notificationContent = res.data().SingupFromMenu
    })
  }


  getSignupTerms() {
    this.CMSSERVICE.getsignUpTerms().then((res) => {
      res.forEach(element => {

        this.topLine = element.data().topLine
        this.secondLine = element.data().SecondLine

      });
    })
  }


  check(email, password) {
    localStorage.setItem('email', email);
    localStorage.setItem('password', password);
  }

  closePopup()
  {
    this._location.back() 
  }
}