import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
@Component({
  selector: 'app-buyer-details-agent',
  templateUrl: './buyer-details-agent.component.html',
  styleUrls: ['./buyer-details-agent.component.css',"../../Misc/CommonCode/seeDetails.css"]
})
export class BuyerDetailsAgentComponent implements OnInit {
  sub: any;
  LookingAddress: string;
  LookingTown: string;
  Lookingpostcode: string;
  Lookingstate: string;
  MaxAmount: string;
  Maxbathroom: string;
  Maxreception: string;
  PropertyCondition: string;
  PropertyType: string;
  Roomsmax: string;
  ownership: string;
  UserId: string;
  userDetail;
  username: any;
  email: any;
  Phone: any;
  address: any;


  UserDetails: any;
  LookingStreetname: any;
  phonenumber: any;
  Email: any;
  constructor(private route: ActivatedRoute, 
    private _location: Location) { }

  ngOnInit() {

    this.UserDetails = JSON.parse(localStorage.getItem("sellerPropertyDetail"));
    const sellerDetail = this.UserDetails.BuyerDetails
    this.Lookingpostcode = sellerDetail.Lookingpostcode
    this.LookingStreetname = sellerDetail.LookingStreetname
    this.username = sellerDetail.username
    this.phonenumber  = sellerDetail.phonenumber
    this.Email = sellerDetail.Email
}

backClicked() {
  localStorage.removeItem("sellerPropertyDetail");
  this._location.back();
}
  }


