  userDetail() {
    this.isLoading = true
  
    this.postcodeService.getLat(this.listingBuyer.Currentpostcode.replace(/\s/g, ""))
      .subscribe(data => {
        this.postcodeCoordinates = data;
       
        this.user.Currentpostcode = this.listingBuyer.Currentpostcode.toUpperCase()
        this.user.CurrentAddress =  this.listingBuyer.CurrentAddress 
        this.user.CurrentTown = this.listingBuyer.CurrentTown
         this.user.Currentstate = this.listingBuyer.Currentstate
        this.return = this.fillFormsService.createBuyerUserCustomer(this.user)
          .then(data => {
            this.isLoading = false
            this.nextStep()
          })
      },
        error => { const dialogRef = this.dialog.open(AltertFormDialogComponent, { data: { message: "Enter Valid Postcode" } }) }
      )

    this.isLoading = false
  }


  otherOption() {
    this.other = false
  }
  otherChain() {
    this.other = true
  }
  financialPostion() {
    this.finanacial = true
  }
  removefinancialPostion() {
    this.finanacial = false
  }








  NewUser() {
    this.newUser = true;
  }

  OldUser() {
    this.newUser = false;
  }
  close() {
    this.newUser = false;
    this.newUser = false;
  }





  continueClose() {
    location.reload()
    this.overlay = false;
    this.isLoggedIn = true
    this.isLoading = true
  }




  radioChange(event) {
    this.filter = event.value;
    if (this.filter == "buy") {
      this.amounts = true
      this.rentOption = false
      this.buyOption = true
    }
    if (this.filter == "rent") {
      this.amounts = true
      this.buyOption = false;
      this.rentOption = true
    }
  }




  public previousStep() {
    this.selectedIndex -= 1;
  }
  public nextStep() {
    this.selectedIndex += 1;

  }








  alreadyUser() {
    this.SignUp = false
  }

  newCustomeer() {
    this.SignUp = true

  }


  /* Confrim Buyer Details */
  submitBuyer() {
    this.isLoading = true
    //Lookup Declared Function
    this.postcodeService.getLat(this.listingBuyer.Lookingpostcode.replace(/\s/g, ""))
      .subscribe(data => {
        this.postcodeCoordinates = data;
        (this.listingBuyer.longitude = this.postcodeCoordinates.result.longitude),
          (this.listingBuyer.latitude = this.postcodeCoordinates.result.latitude),
          this.listingBuyer.UserId = this.userData.uid;
        this.isLoading = true;
        this.listingBuyer.UserId = this.uid
        this.listingBuyer.longitude,
          this.listingBuyer.latitude,
          this.listingBuyer.username = this.user.Name
        this.listingBuyer.Email = this.user.email
        this.listingBuyer.phonenumber = this.user.Phone
        this.listingBuyer.title = this.user.title
        this.listingBuyer.DOB = this.user.DOB
        this.listingBuyer.Lookingpostcode = this.listingBuyer.Lookingpostcode.toUpperCase()
        this.listingBuyer.Currentpostcode =  this.listingBuyer.Currentpostcode.toUpperCase()
        this.newUser = true
        this.return = this.fillFormsService
          .createCustomer(this.userData.uid, this.listingBuyer)
          .then(element => {
            if (element == true) {
              localStorage.setItem("buyerUserDetails", JSON.stringify(this.listingBuyer));
              this.isLoading = false;
              this.register = false
              this.router.navigate(["/buyerMatchlisting/Lookingpostcode/PropertyType/LookingTown/MinAmount/MaxAmount/latitude/longitude/" + this.newUser + "/PropertyFor/LookingStreetname/Conditions/FinancialPosition/Validity/Position/UserId/ChainStatus/" + this.uid])
            }
          });



      });
  }
  overLay() {

    this.postcodeService.getLat(this.listingBuyer.Lookingpostcode.trim()).subscribe(data => {

      this.register = true
    },
      error => {
        if (error.status) {
          const dialogRef = this.dialog.open(AltertFormDialogComponent, {
            data: { message: "Please enter valid postcode" }
          });
        }
      }

    )

  }


  newclose() {
    this._location.back()
  }
  check(email, password) {
    localStorage.setItem('email', email);
    localStorage.setItem('password', password);
  }

  closePopup() {
    this._location.back()
  }
  getNotificationContent() {
    this.CMSSERVICE.getBlueNotification(this.uid).then((res) => {

      this.notificationContent = res.data().BuyerSignupNotification
    })
  }


  getSignupTerms() {
    this.CMSSERVICE.getsignUpTerms().then((res) => {
      res.forEach(element => {

        this.topLine = element.data().topLine
        this.secondLine = element.data().SecondLine

      });
    })
  }

