import { Component, OnInit } from '@angular/core';
import {AdminPanelService} from "../admin-panel/admin-panel.service.ts.service";
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import { AltertFormDialogComponent } from "../../Misc/alertFormdialog/alertFormdialog.component";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
} from "@angular/material";
import { element } from 'protractor';
import { flatMap } from 'tslint/lib/utils';
@Component({
  selector: 'app-users-edit-page',
  templateUrl: './users-edit-page.component.html',
  styleUrls: ['./users-edit-page.component.css','../Admin-common.css']
})
export class UsersEditPageComponent implements OnInit {
  getUser: any;
  uid: any;
  name: any;
  image: any;
  sub: any;
  UserUid: string;
  title: any;
  email: any;
  DOB: any;
  Phone: any;
  Currentpostcode: any;
  Currentstate: any;
  CurrentTowncity: any;
  Currentaddress: any;
  username: any;
  BuyerUserDetails =[]
  SellerUserDetails=[]
  return: any;
  user:any;
  Name: any;
  prefrence: any;
  isLoading:boolean = false
  confirmedInterest = []
  ExpressedInterest = []

  sellerConfirmedInterset = []
  sellerExpressedInterest = []

  constructor(public adminPanelService:AdminPanelService,
    private _Activatedroute: ActivatedRoute,
    private _location: Location,
    private dialog: MatDialog,
    ) { }

  ngOnInit() {
    this.user  = JSON.parse(localStorage.getItem("user"));
    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.UserUid = params.get("uid");
    })

    this.getUserDetails()
    this.getSelectedUserDetail()
    this.getBuyerEntries()
    this.getSellerEnteries()
    this.getConfirmedInterest()

  }





  deletesellerconfirmedInterest(propid)
  {
    this.isLoading = true
  this.adminPanelService.deleteSellerConFirmInterestBuyer(this.UserUid,propid).then((data)=>{

    this.isLoading = false
    const dialogRef = this.dialog.open(AltertFormDialogComponent, {
      data: { message: "Expressed Interest  Deleted" }
    });
  })
  }

  
  deleteconfirmedInterest(uid,propid)
  {
    this.isLoading = true
  this.adminPanelService.deleteConFirmInterestBuyer(this.UserUid,propid).then((data)=>{

    this.isLoading = false
    const dialogRef = this.dialog.open(AltertFormDialogComponent, {
      data: { message: "Confirmed Interest  Deleted" }
    });
  })
  }

  deleteExpressedInterest(uid,propid)
  {
    this.isLoading = true
  this.adminPanelService.deleteConFirmInterestBuyer(uid,propid).then((data)=>{

    this.isLoading = false
    const dialogRef = this.dialog.open(AltertFormDialogComponent, {
      data: { message: "Expressed Interest  Deleted" }
    });
  })
  }


 getConfirmedInterest()
 {
   this.adminPanelService.getConFirmInterestBuyer(this.UserUid).then((element)=>{
     element.forEach(element => {
       if(element.data().matchStatus == "confirmed")
       {
         this.confirmedInterest.push({Detail:element.data(),id:element.id})

       }
       if(element.data().matchStatus == "pending")
       {
         this.ExpressedInterest.push({Detail:element.data(),id:element.id})
       }
     });
   })

   this.adminPanelService.getSellerConFirmInterestBuyer(this.UserUid).then((seller)=>{
     seller.forEach((sellerElemnt)=>{

      if(sellerElemnt.data().matchStatus == "pending")
      {
        this.sellerExpressedInterest.push({Detail:sellerElemnt.data(),id:sellerElemnt.id})
      }
      console.log( this.sellerExpressedInterest)

      // if(sellerElemnt.data().matchStatus == "pending")
      // {
      //   this.sellerExpressedInterest.push({Detail:sellerElemnt.data(),id:sellerElemnt.id})
      // }
      
     })
   })

 }




 
  deleteBuyer(uid,docid){
    this.adminPanelService.deleteBuyer(uid,docid).then((data)=>{
      const dialogRef = this.dialog.open(AltertFormDialogComponent, {
        data: { message: "Nestimate Deleted" }
      });

    })
  this.BuyerUserDetails = []
  this.getBuyerEntries()
  }

  deleteSeller(uid,docid)
{
  this.adminPanelService.deleteSeller(uid,docid).then((data)=>{
    const dialogRef = this.dialog.open(AltertFormDialogComponent, {
      data: { message: "Nestimate Deleted" }
    });
  })
    this.SellerUserDetails = []
  this.getSellerEnteries()
}

  getUserDetails()
  {
  
    if(this.user != null)
    {
      this.uid = this.user.uid
      document.getElementById("appfooter").style.display = "none"
      document.getElementById("header-main").style.display = "none"
      this.adminPanelService.getUser(this.uid).then((element)=>{
      this.username = element.data().Name
      this.image = element.data().Image
    })
  }
  }


  backClicked() {
    this._location.back();
  }




  getSelectedUserDetail()
  {
  this.adminPanelService.getUser(this.UserUid).then((user)=>{

     this.Name = user.data().Name

   this.email = user.data().email
  if(user.data().title != null)
  {
    this.title = user.data().title

  }
    if( user.data().DOB != null)
    {
     this.DOB = user.data().DOB.toDate() 
    }

    if(user.data().Phone != null)
    {
      this.Phone = user.data().Phone
    }

    if(user.data().Currentpostcode != null)
    {
      this.Currentpostcode = user.data().Currentpostcode
    }

    if(user.data().Currentstate != null)
    {
      this.Currentstate = user.data().Currentstate
    }
    if(user.data().CurrentTowncity != null)
    {
      this.CurrentTowncity = user.data().CurrentTowncity
    }
    if(user.data().Currentaddress != null)
    {
      this.Currentaddress = user.data().Currentaddress
    }
    if(user.data().prefrence != null)
    {
      this.prefrence = user.data().prefrence
    }
  })
  }


  getBuyerEntries()
  {
    this.adminPanelService. getBuyerEntries(this.UserUid).then(user=>{
      user.forEach(UserDetail => {
        this.BuyerUserDetails.push({detail:UserDetail.data(),id:UserDetail.id})
      });

    })
   
  }


  getSellerEnteries()
  {
    this.adminPanelService. getSellerEntries(this.UserUid).then(user=>{
      user.forEach(UserDetail => {
        this.SellerUserDetails.push({detail:UserDetail.data(),id:UserDetail.id})
      });

    })
    
  }
  
 
  userDetails(){
    this.isLoading = true
       this.user.uid = this.UserUid
       this.user.Name = this.Name
       this.user.email = this.email
      if(this.title != undefined)
      {
        this.user.title = this.title
      }
     else
     {
      this.user.title = null
     }
     if(this.DOB != undefined)
     {
      this.user.DOB = this.DOB
     }
     else
     {
      this.user.DOB = null
     }
     if(this.Phone != undefined)
     {
      this.user.Phone = this.Phone
     }
      else
      {
        this.user.Phone = null
      }
       if(this.title != undefined)
       {       
         this.user.title = this.title
       }
       else{
        this.user.title = null
       }
       if(this.Currentaddress != undefined)
       {
        this.user.Currentaddress = this.Currentaddress
       }
      else
      {
        this.user.Currentaddress = null
      }
      if(this.Currentpostcode != undefined)
      {
        this.user.Currentpostcode = this.Currentpostcode
      }
      else
      {
        this.user.Currentpostcode = null
      }
      if(this.Currentstate != undefined)
      {
        this.user.Currentstate = this.Currentstate
      }
      else
      {
        this.user.Currentstate = null
      }

      if(this.CurrentTowncity != undefined)
      {
        this.user.CurrentTowncity = this.CurrentTowncity
      }
      else
      {
        this.user.CurrentTowncity = null;
      }   
      if(this.prefrence != undefined)
      {
        this.user.prefrence = this.prefrence
      }
      else
      {
        this.user.prefrence = null;
      }
        this.return =  this.adminPanelService.userupate(this.user)
        .then(data => {
          this.isLoading = false
          const dialogRef = this.dialog.open(AltertFormDialogComponent, {
            data: { message: "User Detail Updated" }
          });
        });

      }
 
}
