import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import { notification } from "../../../Model/notification"
import { StateServiceService } from "../../../state-service.service";
import { SellerMatchesService } from "./seller-matches.service";
import { HttpService } from "../../../http.service";
import { CMSSERVICE } from "../../../cms.service"
import { NavigationEnd } from '@angular/router';
@Component({
  selector: 'app-seller-matches',
  templateUrl: './seller-matches.component.html',
  styleUrls: ['./seller-matches.component.css', '../../../Misc/CommonCode/seeDetails.css']
})
export class SellerMatchesComponent implements OnInit {
  Lookingpostcode: any;
  Lookingstate: any;
  LookingAddress: any;
  norooms: any;
  PropertyCondition: any;
  MinAmount: any;
  sub: any;
  PropertyType: string;
  ownership: string;
  features: string;
  matchStatus: any;
  MaxAmount: string;
  propertyId: any;
  return: any;
  uid: any;
  user: any;
  expressed: any;
  Type: string;
  ChainStatus: string;
  Conditions: string;

  Position: string;
  notification: notification;
  now: Date = new Date();
  UserId: string;
  matchesSeller: any;
  sellerProperty = [];
  overLay: boolean = false
  id: string;
  buyerId: string;
  notificationOverlay: Object;
  notificationContent: any;
  PropertyFor: string;
  buyingPosition: any;
  buyerFinancialPosition: any;
  BuyerPropertyType: any;
  BuyerLookingpostcode: any;
  BuyerRoommin: any;
  BuyerRoomsmax: any;
  username: any;
  usertitle: any;
  buyerPropertyFor: any;
  Ownership: any;
  LookingStreetname: any;
  Maxbathroom: any;
  Maxreception: any;
  buyerPropertyId: string;
  buyerUserId: string;
  SellerPropertyID: string;
  buyerDetails;
  BuyerPropertyId: string;
  BuyerUserId: string;
  SellerPropertyId: string;
  UserDetails: any;
  Roommin: any;
  Roomsmax: any;
  constructor(private _Activatedroute: ActivatedRoute,
    private _router: Router,
    private _location: Location,
    private SelectedMyMatchesService: SellerMatchesService,
    private stateService: StateServiceService,
    public HttpService: HttpService,
    public CMSSERVICE: CMSSERVICE) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    if (this.user != null) {
      this.uid = this.user.uid;
    }
    this._router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
    });
    const details = JSON.parse(localStorage.getItem("matchesSeller"))
    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.expressed = params.get("expressed");
      this.propertyId = params.get("propertyId")
    });


    this.getBuyerDetails()



    this.CMSSERVICE.getBlueNotification(this.uid).then((element) => {
      this.notificationContent = element.data().MynestimateBuyerDetails
    })

  }
  backClicked() {
    this._router.navigate(["/myListing/myNestimate/action"]).then(()=>{
      window.location.reload();
    })
  }


  backOverlay() {
    this.overLay = false
  }
  connfirmOverLay() {
    this.overLay = true
  }

  getBuyerDetails() {
    const details = JSON.parse(localStorage.getItem("matchesSeller"))
   
    this.UserDetails = details.detail
    this.MinAmount = this.UserDetails.BuyerDetails.MinAmount
    this.MaxAmount = this.UserDetails.BuyerDetails.MaxAmount
    this.PropertyFor = this.UserDetails.BuyerDetails.PropertyFor
    this.Ownership = this.UserDetails.BuyerDetails.Ownership
    this.PropertyType = this.UserDetails.BuyerDetails.PropertyType
    this.Lookingpostcode = this.UserDetails.BuyerDetails.Lookingpostcode
    this.LookingStreetname = this.UserDetails.BuyerDetails.LookingStreetname
    this.Roommin = this.UserDetails.BuyerDetails.Roommin
    this.Roomsmax = this.UserDetails.BuyerDetails.Roomsmax
    this.Conditions = this.UserDetails.BuyerDetails.Conditions

  }


  

}

