import { Component, OnInit } from "@angular/core";
import { SellerSelectedPropertyDetailService } from "./sellerSelectedPropertyDetail.service";
import { StateServiceService } from "../../../../../.././state-service.service";
import { SellerMatchListingService } from "../sellerMatchListing.service";
import { SelectedPropertyDialogComponent } from "../../../../../.././Misc/selectedPropertyDialog/selectedPropertyDialog.component";
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
} from "@angular/material";
import { matchesSeller } from "../../../../../../Model/matchesSeller";
import { matchesBuyer } from "../../../../../../Model/matchesBuyer";
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import { notification } from "../../../../../../Model/notification";
import { HttpService } from "../../../../../../http.service";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import {CMSSERVICE} from "../../../../../../cms.service"
import { NavigationEnd } from '@angular/router';
import {AlertSelectedPropertyComponent} from "../../../../../../Misc/alert-selected-property/alert-selected-property.component"


@Component({
  selector: "app-Propertydetails",
  templateUrl: "./sellerSelectedPropertyDetail.component.html",
  styleUrls: ["./sellerSelectedPropertyDetail.component.css","../../../../../../Misc/CommonCode/seeDetails.css"],
})
export class SellerSelectedPropertyComponent implements OnInit {
  user: any;
  uid: any;
  return: any;
  isLoggedIn: boolean;
  isLoading: boolean = false;
  property: any;
  version = VERSION;
  userData: any;
  matchesSeller: any;
  matchesBuyer: any;
  isSellerSelected: boolean = false;
  listingSeller: any;
  Buyerproperty: any;
  sub: any;
  LookingStreetname: string;
  Lookingpostcode: string;
  PropertyType: string;
  Conditions: string;
  ChainStatus: string;
  sellerproperty: any;
  sellerProperty: any;
  Firstname: string;
  Lastname: string;
  Email: string;
  DOB: any;
  FinancialPosition: string;

  PriceRange: string;
  Validity: string;
  Type: string;
  Position: string;
  UserId: string;
  norooms: string;
  matchStatus: any;
  Roomsmax: string;
  ownership: string;
  Maxbathroom: string;
  Maxreception: string;
  features: string;
  overlay: boolean = false;
  title: any;
  unNamed: any;
  propertyId: string;
  datastored: boolean = false;
  express: boolean = true;
  propertyIdCheck: any;
  expressed: any;
  notification: notification;
  now: Date = new Date();
  Look_State: string;
  Look_rooms: string;
  Look_Propertycondition: string;
  Look_Address: string;
  Look_ownership: string;
  Look_features: string;
  Look_userId: string;
  Look_postcode: any;
  Look_PropertyType: string;
  Look_maxAmount: any;
  Look_Town: string;
  MinAmount: string;
  Look_Maxreceptions: string;
  Look_Maxbathrooms: string;
  Look_Maxrooms: string;
  PropertyFor: string;
  Look_Propertyfor: string;
  look_id: any;
  UserDetails: any;
  notificationOverlay: Object;
  notificationContent: any;
  buyingPosition: any;
  buyerFinancialPosition: any;
  BuyerPropertyType: any;
  BuyerLookingpostcode: any;
  BuyerRoommin: any;
  BuyerRoomsmax: any;
  username: any;
  usertitle: any;
  buyerPropertyFor: any;
  sellerDetails: any;
  buyerDetails: any;
  LookingTown: any;
  Minreception: any;
  Ownership: any;
  Minbathroom: any;
  ExpressedNestimate: any;
  OtherError: any ="Other"
  BuyerUserId: any;
  BuyerRecentCheck=[]
  constructor(
    private seller_Selected_propertydetail_Service: SellerSelectedPropertyDetailService,
    private stateService: StateServiceService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private SellerMatchListingService: SellerMatchListingService,
    private _Activatedroute: ActivatedRoute,
    private _router: Router,
    private _location: Location,
    public HttpService: HttpService,
    public HttpClient: HttpClient,
    public CMSSERVICE:CMSSERVICE
  ) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;




    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.PriceRange = params.get("MaxAmount");
      this.MinAmount = params.get("MinAmount");
      this.Lookingpostcode = params.get("Lookingpostcode");
      this.LookingStreetname = params.get("LookingStreetname");
      this.Position = params.get("Position");
      this.PropertyType = params.get("PropertyType");
      this.Roomsmax = params.get("Roomsmax");
      this.ownership = params.get("Ownership");
      this.Conditions = params.get("Conditions");
      this.Maxbathroom = params.get("Maxbathroom");
      this.Maxreception = params.get("Maxreception");
      this.features = params.get("features");
      this.UserId = params.get("UserId").replace(/\s/g, "");
      this.FinancialPosition = params.get("FinancialPosition");
      this.ChainStatus = params.get("ChainStatus");

      this.propertyId = params.get("propertyId");
      this.expressed = params.get("expressed");
      this.PropertyFor = params.get("PropertyFor")

      //SelectedSeller Params
      this.Look_State = params.get("Look_state");
      this.Look_rooms = params.get("Look_rooms");
      this.Look_Propertycondition = params.get("Look_Propertycondition");
      this.Look_Address = params.get("Look_Address");
      this.Look_ownership = params.get("Look_ownership");
      this.Look_features = params.get("Look_features");
      this.Look_userId = params.get("Look_userId");
      this.Look_postcode = params.get("Look_postcode");
      this.Look_PropertyType = params.get("Look_PropertyType");
      this.Look_maxAmount = params.get("Look_maxAmount");
      this.Look_Town = params.get("Look_Town");
      this.Look_Address = params.get("Look_Address");
      this.Look_Maxbathrooms = params.get("Look_Maxbathrooms");
      this.Look_Maxreceptions = params.get("Look_Maxreceptions");
      this.Look_ownership = params.get("Look_ownership");
      this.Look_Maxrooms = params.get("Look_Maxrooms")
      this.Look_Propertyfor = params.get("Look_PropertyFor")
      this.look_id = params.get("Look_id")
    });


this.getBuyerDetails()
this.getSellerDetails()


this.CMSSERVICE.getBlueNotification(1).then((data)=>{
  this.notificationContent = data.data().SellerMatchesExpressInterestNotifcation
})
this.CMSSERVICE.getOtherError(this.OtherError).then((element) => {
  this.ExpressedNestimate= element.data().ExpressedNestimate
 })
this._router.events.subscribe((evt) => {
  if (!(evt instanceof NavigationEnd)) {
      return;
  }
  window.scrollTo(0, 0)
});


  }

  //Create Database match Seller
  submitForm() {
    this.isSellerSelected = true;
    this.matchesSeller = {
      BuyerDetails:this.buyerDetails,
      BuyerUserId:this.UserId,
      BuyerPropertyId:this.propertyId,
      SellerUserId:this.uid,
      SellerPropertyId:this.look_id,
      matchStatus: "pending",
      Entry: "Seller",
 
    };
    this.isSellerSelected = true;
    this.return = this.seller_Selected_propertydetail_Service
      .matchesSellerCreate(this.uid, this.matchesSeller)
      .then((data) => {
        if (data == true) {
          this.isSellerSelected = false;
          this.overlay = false
        }
      });
    this.matchesBuyer = {
    SellerDetails:this.sellerDetails,
    BuyerUserId:this.UserId,
    BuyerPropertyId:this.propertyId,
    SellerUserId:this.uid,
    SellerPropertyId:this.look_id,
      matchStatus: "confirm_interest",
      Entry: "Seller"
    };

    this.isSellerSelected = true;
    this.return = this.seller_Selected_propertydetail_Service
      .matchesBuyerCreate(this.UserId, this.matchesBuyer)
      .then((data) => {
        if (data == true) {
          this.isSellerSelected = false;
          this.datastored = true;
          this.express = false;
          this.createSellerNotification()
          this.addToExpressCollection();
        }
      });
   

  }

  Overlayopen() {
    this.overlay = true
  }
  continueClose() {
    this.overlay = false
  }
  backClicked() {
    this._location.back();
  }
  addToExpressCollection() {
    this.return = this.seller_Selected_propertydetail_Service.ExpressInterest(
      this.uid,
      this.propertyId.trim()
    ).then((data) => {
      if (data == true) {
        this.isSellerSelected = false;
        this.datastored = true;
        this.express = false;
        const dialogRef = this.dialog.open(AlertSelectedPropertyComponent, {
          data: { message: this.ExpressedNestimate }
        });
    
      
      }
    });
  }
  createSellerNotification() {
    this.notification = {
      time: this.now,
      viewed: "Confirmed",
      userId: this.uid,
      Type: "Seller_Confirmed_my_nestimate",
      propertyId: this.propertyId.trim(),
      Lastseen:null
    }
    this.return = this.seller_Selected_propertydetail_Service
      .createNotification(this.UserId, this.notification)
      .then(data => {
      });
  }


  getBuyerDetails()
  {    
    this.seller_Selected_propertydetail_Service.getBuyerRequirement(this.UserId,this.propertyId ).then((res)=>{
     this.buyerDetails = res.data()
      this.buyingPosition = res.data().Position
      this.buyerFinancialPosition = res.data().FinancialPosition
      this.BuyerPropertyType  = res.data().PropertyType
      this.BuyerLookingpostcode  = res.data().Lookingpostcode
      this.BuyerRoommin  = res.data().Roommin
      this.BuyerRoomsmax  = res.data().Roomsmax
      this.username = res.data().username
      this.usertitle  = res.data().usertitle
      this.buyerPropertyFor  = res.data().PropertyFor
      this.ChainStatus = res.data().ChainStatus
      this.Conditions = res.data().Conditions
      this.LookingTown = res.data().LookingTown
      this.Maxreception = res.data().Maxreception
      this.Minreception = res.data().Minreception
      this.Ownership = res.data().Ownership
      this.Maxbathroom = res.data().Maxbathroom
      this.Minbathroom = res.data().Minbathroom
      this.BuyerUserId = res.data().UserId
     this.Recentlyviewed()
    })
 
  }

  getSellerDetails()
  {
    this.seller_Selected_propertydetail_Service.getSeller(this.uid,this.look_id).then((res)=>{
      this.sellerDetails = res.data()
    })

  }

  Recentlyviewed()
  {
       
      
    this.seller_Selected_propertydetail_Service.getSellerRecentlyItems(this.uid,this.propertyId).then((getData) => {
       if(getData.data() == undefined)
       {
        this.buyerDetails.time = this.now
        this.seller_Selected_propertydetail_Service.RecentlyView(this.uid,this.buyerDetails,this.propertyId).then((res)=>{
        })
       }
    });  
  }
}
