import { Component, OnInit } from '@angular/core';
import { MatchesBuyerService } from "./matches-buyer.service";
import { Router, ActivatedRoute } from "@angular/router";
import { Location } from "@angular/common";
import { notification } from "../../../Model/notification"
import { HttpService } from "../../../http.service";
import {CMSSERVICE} from "../../../cms.service";
import { NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-matches-buyer',
  templateUrl: './matches-buyer.component.html',
  styleUrls: ['./matches-buyer.component.css','../../../Misc/CommonCode/seeDetails.css','../../../Misc/CommonCode/overlay.css','../../../Misc/CommonCode/Photosseller.css']
})
export class MatchesBuyerComponent implements OnInit {
  Lookingpostcode: any;
  Lookingstate: any;
  LookingAddress: any;
  norooms: any;
  PropertyCondition: any;
  MinAmount: any;
  sub: any;
  PropertyType: string;
  ownership: string;
  features: string;
  matchStatus: any;
  MaxAmount: string;
  propertyId: any;
  return: any;
  uid: any;
  user: any;
  expressed: string;
  userId: string;
  notification: notification;
  now: Date = new Date();
  sellerProperty: any;
  sellerPropertys = [];
  matchesSeller: any;
  overLay: boolean = false;
  buyerProperty;
  sellerId: string;
  notificationOverlay: Object;
  notificationContent: any;
  price: any;
  postcode: any;
  Streetname: any;
  PropertyTypes: any;
  Maxroom: any;
  ownerships: any;
  PropertyFor: any;
  Bathrooms: any;
  Reception: any;
  userName: any;
  usertitle: any;
  BuyerPropertyId: string;
  BuyerUserId: string;
  SellerPropertyId: string;
  SellerUserId: string;
  buyerDetails: any;
  userData: any;
  LookingTown: any;
  propid: string;
  allImages =[]
  ImagesOpen:boolean = false
  constructor(private MatchesBuyerService: MatchesBuyerService,
    private _Activatedroute: ActivatedRoute,
    private _router: Router,
    private _location: Location,
    public HttpService: HttpService,
    public CMSSERVICE:CMSSERVICE

  ) { }

  ngOnInit() {


    this.user = JSON.parse(localStorage.getItem("user"));
    if (this.user != null) {
      this.uid = this.user.uid;
    }
    this._router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
    });
    
 
    const details = JSON.parse(localStorage.getItem("buyermatchespropertydetail"));
 

    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.expressed = params.get("expressed");
      this.propid = params.get("propId")
    });
    this.getSellerDetails()
    this.getBuyerDetails()



this.CMSSERVICE.getBlueNotification(1).then((data)=>{
  this.notificationContent = data.data().BuyerActionNeeded
})



  }
  addToExpressCollection() {
    this.return = this.MatchesBuyerService.ExpressInterest(
      this.uid,
      this.propid
    ).then((data) => {
      if (data == true) {
        this.createSellerNotification();
        this.backClicked();
      }
    });
  }
  createSellerNotification() {
    this.notification = {
      time: this.now,
      viewed: "Confirmed",
      userId: this.uid,
      Type: "Buyer_Confirmed_my_activity",
      propertyId: this.SellerPropertyId ,
      Lastseen: null
    }
    this.return = this.MatchesBuyerService
      .createNotification(this.SellerUserId, this.notification)
      .then(data => {
      });
  }


  backClicked() {
    this._router.navigate(["/myListing/myNestimate/action"]).then(()=>{
      window.location.reload();
    })
  }


  createOther() {
    this.matchesSeller = {
      BuyerDetails: this.buyerDetails,
      SellerDeatils:this.sellerProperty,
      BuyerPropertyId: this.BuyerPropertyId,
      BuyerUserId: this.BuyerUserId,
      SellerUserId: this.SellerUserId,
      SellerPropertyId: this.SellerPropertyId,
      matchStatus: "confirm_interest"

    };
    this.return = this.MatchesBuyerService
      .matchesBuyerCreate(this.SellerUserId, this.matchesSeller)
      .then((data) => {
        if (data == true) {
          this.addToExpressCollection()
        }
      });
  }




  backOverlay() {
    this.overLay = false
  }
  connfirmOverLay() {
    this.overLay = true
  }

  getSellerDetails() {
    const details = JSON.parse(localStorage.getItem("buyermatchespropertydetail"));
    this.userData = details.detail    
    this.sellerProperty = details.detail.SellerDetails
    this.BuyerPropertyId =  this.userData.BuyerPropertyId
    this.SellerPropertyId =  this.userData.SellerPropertyId
    this.SellerUserId =  this.userData.SellerUserId
    this.BuyerUserId = this.uid
    this.price = this.userData.SellerDetails.MaxAmount
    this.postcode = this.userData.SellerDetails.Lookingpostcode
    this.Streetname = this.userData.SellerDetails.Lookingstate
    this.PropertyFor = this.userData.SellerDetails.PropertyFor
    this.PropertyTypes = this.userData.SellerDetails.PropertyType
    this.Maxroom = this.userData.SellerDetails.Maxrooms
    this.ownerships = this.userData.SellerDetails.ownership
    this.Bathrooms = this.userData.SellerDetails.Maxbathrooms
    this.Reception = this.userData.SellerDetails.Maxreception
    this.LookingTown = this.userData.SellerDetails.LookingTown

    this.allImages = this.userData.SellerDetails.Images
  this.matchStatus = this.userData.matchStatus
  }
  getBuyerDetails() {

    this.MatchesBuyerService.getBuyer(this.BuyerUserId, this.BuyerPropertyId).then((res) => {
      this.buyerDetails = res.data()
    })
  }

  OpenImage()
  {
    this.ImagesOpen = true
  }
  closeImage()
  {
    this.ImagesOpen = false
  }

}
