  async buyerupload(event) {
    this.isLoading = true
    const file = event.target.files[0];
    if (file.size > 2000000) {
      const dialogRef = this.dialog.open(AltertFormDialogComponent, {
        data: { message: "Image size must be less than 2mb" }
      });
    }
    else {

      if (file) {
        const filePath = `${this.basePath}/${file.name}`;  // path at which image will be stored in the firebase storage
        this.task = this.afStorage.upload(filePath, file);    // upload task
        // this.progress = this.snapTask.percentageChanges();
        (await this.task).ref.getDownloadURL().then(url => {
          this.buyer = url;
          this.getPropertyMeta(url)
        });  // <<< url is found here
      }
    }

    this.isLoading = false
  }

