import { Component, ViewChild,
  OnInit,
  Inject,
  ElementRef,
  PLATFORM_ID} from '@angular/core';
import { FormsService } from '../../../Home/Property/fillFormBuyer/fillFormBuyer.service';
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { EdiProfileComponent } from "./ediProfileSubmission.component"
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
  MatAutocompleteSelectedEvent,
  MatAutocompleteTrigger,
  MatDialogConfig
} from "@angular/material";
import { FormControl, Validators, FormGroup } from "@angular/forms";
import { HttpService } from "../../../http.service"
import { Location } from "@angular/common";
import { CMSSERVICE } from "../../../cms.service";
import {AuthService} from "../../../auth.service"
@Component({
  selector: 'app-editProfile',
  templateUrl: './editProfile.component.html',
  styleUrls: ['./editProfile.component.css']
})
export class EditProfileComponent implements OnInit {
  @ViewChild('Names',{static:false}) NameView : ElementRef
 @ViewChild ('phone',{static:false}) phoneView:ElementRef
 @ViewChild ('dateofbirth',{static:false}) dateofbirthView:ElementRef
 @ViewChild ('addressed',{static:false}) addressView:ElementRef
  user: any;
  return: any;
  uid: any;
  DOB: any;
  isLoading: boolean = false
  getUser: any;
  dataItem: any;
  Name: any;
  Email: any;
  Phone: any;
  Currentaddress: any;
  email: any;
  prefrence: any;
  id: any;
  getUsers: any;
  edit:boolean = false
  FullName: any;
  Emails: any;
  Phones: any;
  DOBs: any;
  ValidateDob: any;
  Address: any;
  Prefrence: any;
  MyDetail : any = "MyDetails" 
  PersonalDetail: any = "PersonalDetail"
  PhoneValidation: any;
  PhoneLength: any;
  constructor(private fillFormsService: FormsService,
    private router: Router, private dialog: MatDialog,
    public HttpService: HttpService,
    private _location: Location,
    public CMSSERVICE: CMSSERVICE,
    public AuthService:AuthService) { }

  ngOnInit() {

    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;
     this.getOtherForm()
     this.getSeekerPersonalDetailForm()
    this.fillFormsService.getUser(this.uid).subscribe((ref) => {
      ref.forEach((element) => {
        if(element.data().uid == this.uid)
     {
     this.user.Name = element.data().Name
     this.user.Email = element.data().email
     if( element.data().DOB != null)
     {
      this.user.DOB = element.data().DOB.toDate() 
     }
     if( element.data().Phone != null)
     {
      this.user.Phone = element.data().Phone
     }
     if(element.data().Currentaddress  != null)
     {
      this.user.Currentaddress = element.data().Currentaddress
     }
     if(element.data().prefrence  != null)
     {
      this.user.prefrence= element.data().prefrence
     }
     
     }
      });
    });
  }
  emailFormControl = new FormControl("", [
    Validators.required,

  ]);

  dobFormControl = new FormControl("", [
    Validators.required,

  ]);
  phoneFormControl = new FormControl("", [
    Validators.required,
    Validators.minLength(10),
    Validators.maxLength(10),

  ]);
  addressFormControl = new FormControl("", [
    Validators.required,

  ]);

  userDetail(Name, email, Phone, DOB, Address, prefrence) {
    if (Phone != null) {

      var Mobile = parseInt(this.user.Phone)
    }
    if (Name == null || Name.length == 0) {
        const dialogRef = this.dialog.open(EdiProfileComponent, {
          data: { message:  this.FullName  }
        });
      this.NameView.nativeElement.focus()
      document.getElementById("name").style.color = "red"

    }
    else if (email == null) {
      const dialogRef = this.dialog.open(EdiProfileComponent, {
        data: { message: this.Emails }
      });
      document.getElementById("name").style.color = "red"


    }
    else if (Phone == null || Phone.length == 0) {

      const dialogRef = this.dialog.open(EdiProfileComponent, {
        data: { message: this.Phones}
      });
      this.phoneView.nativeElement.focus()
      document.getElementById("name").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("phone").style.color = "red"


    }
    else if (Mobile != Phone) {
      const dialogRef = this.dialog.open(EdiProfileComponent, {
        data: { message: this.PhoneValidation}
      });
      this.phoneView.nativeElement.focus()
      document.getElementById("name").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("phone").style.color = "red"

    }

    else if (Phone.length < 10 || Phone.length > 10) {
      const dialogRef = this.dialog.open(EdiProfileComponent, {
        data: { message: this.PhoneLength }
      });
      this.phoneView.nativeElement.focus()
      document.getElementById("name").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("phone").style.color = "red"

    }
    else if (DOB == null || DOB.length == 0) {
      const dialogRef = this.dialog.open(EdiProfileComponent, {
        data: { message: this.DOBs }
      });
      this.dateofbirthView.nativeElement.focus()
      document.getElementById("name").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("phone").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("dob").style.color = "red"
    }
    else if (Math.floor(Math.abs(Date.now() - new Date(DOB).getTime()) / (1000 * 3600 * 24) / 365.25)
      < 18) {
      const dialogRef = this.dialog.open(EdiProfileComponent, {
        data: { message:  this.ValidateDob }
      });
      document.getElementById("name").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("phone").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("dob").style.color = "red"
    }
    else if (Address == null || Address.length == 0) {
      const dialogRef = this.dialog.open(EdiProfileComponent, {
        data: { message:  this.Address  }
      });
      this.addressView.nativeElement.focus()
      document.getElementById("name").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("phone").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("dob").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("address").style.color = "red"
    }
    else if (prefrence == null) {
      const dialogRef = this.dialog.open(EdiProfileComponent, {
        data: { message: this.Prefrence }
      });
      document.getElementById("name").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("phone").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("dob").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("address").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("prefrence").style.color = "red"

    }
    else {
      this.isLoading = true
      document.getElementById("name").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("phone").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("dob").style.color ="var(--DARK-BLUE-COLOR)"
      document.getElementById("address").style.color ="var(--DARK-BLUE-COLOR)"
      //document.getElementById("prefrence").style.color ="var(--DARK-BLUE-COLOR)"

      this.userDetails()

    }
  }

  userDetails(){
    this.isLoading = true
        this.return = this.fillFormsService.userupate(this.user)
        .then(data => {
       this.isLoading = false
       this.edit = false
       this.alert()
        });
      }

  alert() {
    const dialogRef = this.dialog.open(EdiProfileComponent, {
      data: { message: "Your Profile has been updated" }
    });
  }

  backClicked() {
    this._location.back();
  }


  editUser()
  {
   this.edit = true
  }
  NotEdit()
  {
    this.edit = false
  }

  getOtherForm() {
    this.CMSSERVICE.getMyDetailError(this.MyDetail).then((element) => {
     this.FullName = element.data().FullName
     this.Emails= element.data().Email
     this.Phones= element.data().Phone
     this.DOBs = element.data().DOB
     this.ValidateDob = element.data().ValidateDob
     this.Address  = element.data().Address
     this.Prefrence = element.data().Prefrence
      this.isLoading = false
      
    })

  }
  getSeekerPersonalDetailForm() {
    this.CMSSERVICE.getSeekerPersonalError(this.PersonalDetail).then((element) => {
  

      this.PhoneValidation = element.data().PhoneValidation
      this.PhoneLength = element.data().PhoneLength

      this.isLoading = false
    })

  }

  updatePassword(data)
  {
    this.AuthService.UpdatePassword(data)
  }
 
}