import { Injectable } from "@angular/core";
import {
  AngularFirestore,
  AngularFirestoreCollection
} from "@angular/fire/firestore";

@Injectable({
  providedIn: "root"
})
export class MyListingService {
  customersRef: any;
  SellerRef:any;
  itemscollection: any;
uid:any
  return: any;
  constructor(private db: AngularFirestore) {
    this.customersRef = db.collection("listingBuyer");
    this.SellerRef = db.collection("listingSeller");

    
  }

  getRecentlyItems(uid):any
  {
    return this.db
    .collection("Recentlyviewed")
    .doc(uid)
    .collection("Viewed")
    .ref.get()
  }
  getSellerRecentlyItems(uid):any
  {
    return this.db
    .collection("Recentlyviewed")
    .doc(uid)
    .collection("Sellerviewed")
    .ref.get()
  }

  getFavorate(uid):any{
    return this.db
    .collection("Favorate")
    .doc(uid)
    .collection("FavorateItem")
    .ref.get()
  }
  getBuyerRequirement(uid): Promise<any> {
    return this.customersRef
      .doc(uid)
      .collection("requirements")
      .ref.get();
  }
  getSellerProperties(uid): Promise<any> {
    return this.SellerRef
      .doc(uid)
      .collection("properties")
      .ref.get();
  }

  deleteCoffeeOrder(uid,docid) {
   return this.SellerRef
        .doc(uid)
        .collection("properties")
        .doc(docid)
        .delete();
 }
 deleteBuyer(uid,docid) {
  return this.customersRef
       .doc(uid)
       .collection("requirements")
       .doc(docid)
       .delete();
}
deletefavorate(uid,docid) {
  return this.db
      .collection("Favorate")
       .doc(uid)
       .collection("FavorateItem")
       .doc(docid)
       .delete();
}

sellerFavorates(uid):any
  {
  return this.db
  .collection("Favorate")
  .doc(uid)
  .collection("SellerFavorateItem")
  .ref.get()
  }
  sellerdeletefavorate(uid,docid) {
    return this.db
        .collection("Favorate")
         .doc(uid)
         .collection("SellerFavorateItem")
         .doc(docid)
         .delete();
  }


  recentlySellerDelete(uid,propid):any
  {
    return this.db
    .collection("Recentlyviewed")
    .doc(uid)
    .collection("Sellerviewed")
     .doc(propid)
     .delete();
  }

  recentlyBuyerDelete(uid,propid):any
  {
    return this.db
    .collection("Recentlyviewed")
    .doc(uid)
    .collection("Viewed")
     .doc(propid)
     .delete();
  }
}
