import { Injectable } from "@angular/core";
import {
  AngularFirestore,
  AngularFirestoreCollection
} from "@angular/fire/firestore";

@Injectable({
  providedIn: "root"
})
export class SellerMatchListingService {
  customersRef: any;
  propertiesRef: any;
  Userref:any;
  InterestRef: AngularFirestoreCollection<unknown>;
  Interest: any;
  sellerMatchImages: any;
  return: void;
  constructor(private db: AngularFirestore) {
    this.customersRef = db.collection("listingSeller");
    // Collection Group
    this.propertiesRef = db.collectionGroup("requirements");
    this.Userref=db.collection("users");
    
    //Express Interest
    this.InterestRef = db.collection("expressInterest");
    this.Interest = db.collectionGroup("Express");



    this.sellerMatchImages = db.collection("MatchPageImages")
  }
  getSellerProperties(uid): any {
    return this.propertiesRef.get();
  }
 

  getUser(uid):any{
    return this.Userref.get();
  }
  
  ExpressInterest(uid) {
    return this.db
      .collection("expressInterest")
      .doc(uid)
      .collection("Express")
      .get();
  }


getMatchPageImages(uid):any
{
  return this.sellerMatchImages.ref.get();
}
Favorates(uid)
  {
  return this.db
  .collection("Favorate")
  .doc(uid)
  .collection("SellerFavorateItem")
  .ref.get()
  }
  deletefavorate(uid,docid) {
    return this.db
        .collection("Favorate")
         .doc(uid)
         .collection("SellerFavorateItem")
         .doc(docid)
         .delete();
  }
  async Favorate(key, favorate,id) {
    this.return = await this.db
      .collection("Favorate")
      .doc(key)
      .collection("SellerFavorateItem")
      .doc(id)
      .set({...favorate})
      .then(function (data) {
        
      });
    return true;
  }
}
