import { Injectable, NgZone } from "@angular/core";
import { User } from "./shared/services/user";
import { auth } from "firebase/app";
import { AngularFireAuth } from "@angular/fire/auth";
import {
  AngularFirestore,
  AngularFirestoreDocument
} from "@angular/fire/firestore";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Location } from "@angular/common";
import {LoginUserErrorComponent} from './Misc/LoginUserError/LoginUserError.component';
import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA,
} from "@angular/material";
@Injectable({
  providedIn: "root"
})
export class AuthService {
  Name: string;
  user: any;
  displayName: any;
  userData: any; // Save logged in user data
  logout() {
    throw new Error("Method not implemented.");
  }


  constructor(
    public afs: AngularFirestore, // Inject Firestore service
    public afAuth: AngularFireAuth, // Inject Firebase auth service
    public router: Router,
    public ngZone: NgZone, // NgZone service to remove outside scope warning
    private _location: Location,
    private dialog: MatDialog,
  ) {
    /* Saving user data in localstorage when 
    logged in and setting up null when logged out */
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
      } else {
        localStorage.setItem("user", null);
      }
    });
  }

  // Sign in with email/password
  async SignIn(email, password) {
    try {
      const result = await this.afAuth.auth.signInWithEmailAndPassword(
        email,
        password
      );
      this.ngZone.run(() => {
        window.location.reload()
      });
     // this.SetUserData(result.user);
    } catch (error) {

      const dialogRef = this.dialog.open(LoginUserErrorComponent, {
        data: { message: "Incorrect email or password" }
      });
      //window.alert(error);
    }
  }

  // Sign up with email/password
  async SignUp(email,password,displayName,Users) { 
    try {
      const result = await this.afAuth.auth.createUserWithEmailAndPassword(email,password);
     this.SetUserDatas(result.user,displayName,Users);
    } 
    catch (error) {
      window.alert(error.message);
    }
  }

  UpdatePassword(data)
  {
    this.userData.updatePassword(data).then(function(){
      //Success
    }).catch(function(error){
      console.log(error)
      //Failed
    });
    
  }


 

  // Send email verfificaiton when new user sign up
  // async SendVerificationMail() {
  //   await this.afAuth.auth.currentUser.sendEmailVerification();
  //   this.router.navigate(["verify-email-address"]);
  // }

  // Reset Forggot password
  async ForgotPassword(passwordResetEmail) {
    try {
      await this.afAuth.auth.sendPasswordResetEmail(passwordResetEmail);
      alert("Reset password link sent on your registered email id")
      this.router.navigate(["/"])
    } catch (error) {
      window.alert(error);
    }
  }

  // Returns true when user is looged in and email is verified
  get isLoggedIn(): boolean {
    const user = JSON.parse(localStorage.getItem("user"));
    return user !== null && user.emailVerified !== false ? true : false;
  }

  // Sign in with Google
  GoogleAuth() {
    return this.AuthLogin(new auth.GoogleAuthProvider());
  }
   // Sign in with Google
   GoogleAuthSignup() {
    return this.AuthSignup(new auth.GoogleAuthProvider());
  }
  // Sign in with Google
  FacebookAuth() {
    return this.AuthLogin(new auth.FacebookAuthProvider());
  }

  // Auth logic to run auth providers
  async AuthLogin(provider) {
    try {
      const result = await this.afAuth.auth.signInWithPopup(provider);
      const User= "User"
      this.ngZone.run(() => {
        this.SetUserData(result.user);
        this.createUserChecker(result.user,User)
        //window.location.reload()
      });
    } catch (error) {
      window.alert(error);
    }
  }

  async AuthSignup(provider) {
    try {
      const result = await this.afAuth.auth.signInWithPopup(provider);
      this.ngZone.run(() => {
        this.SetUserData(result.user);
     
      });
    } catch (error) {
      window.alert("incorrect username/password needs to change");
    }
  }
  /* Setting up user data when sign in with username/password, 
  sign up with username/password and sign in with social auth  
  provider in Firestore database using AngularFirestore + AngularFirestoreDocument service */
  SetUserData(user) {
    const userRef: AngularFirestoreDocument<any> = this.afs.doc(
      `users/`+ user.uid
    );
    const userData:any= {
      uid: user.uid,
      email: user.email,
      Name :user.displayName,
      UserType:"User"
    };
  
    return userRef.set(userData, {
      merge: true
    }),
    setTimeout(location.reload.bind(location),2000); // milliseconds, so 10 seconds = 10000ms

  
    //window.location.reload()
  }
  SetUserDatas(user,displayName,Users) {
    const userRef: AngularFirestoreDocument<any> = this.afs.doc(
      `users/`+ user.uid
    );
    const userData:any= {
      uid: user.uid,
      email: user.email,
      Name :displayName,
      UserType:Users
    };
    return userRef.set(userData, {
      merge: true
    });

  }

  createUserChecker(user, userType) {
    const userRef: AngularFirestoreDocument<any> = this.afs.doc(
      `CheckUser/`+ user.email
    );
    const userData: any = {
      UserType: userType,
      uid: user.uid,
    };
    return userRef.set(userData, {
      merge: true,
  
    });
  }
 


  
  // Sign out
  async SignOut() {
    await this.afAuth.auth.signOut();
    localStorage.removeItem("user");
    window.location.reload()
    this.router.navigate(['/'])
  .then(() => {
    window.location.reload();
  });

  }



  

}
