  radioChange(event) {
    this.filter = event.value;
    if (this.filter == "buy") {
      this.maxamounts = true
      this.letOption = false
      this.sellOption = true
    }
    if (this.filter == "rent") {
      this.maxamounts = true
      this.sellOption = false;
      this.letOption = true
    }
  }


