import { Component, OnInit , Input} from '@angular/core';
import { map, startWith, take, ignoreElements } from "rxjs/operators";

import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import {SellerNestimateDetails} from "../../Menu/myListings/listing-selling-property-detail/seller-nestimate-details.service"
import { Observable } from 'rxjs';
@Component({
  selector: 'app-images-slider',
  templateUrl: './images-slider.component.html',
  styleUrls: ['./images-slider.component.css']
})
export class ImagesSliderComponent implements OnInit {
  AllImages =[]
  sub: any;
  UserId: string;
  id: string;
  @Input() userID: string;
  @Input() child: string;
  ImagesOpen:boolean = true
  constructor(public ActivatedRoutes:ActivatedRoute , public SellerNestimateDetails:SellerNestimateDetails) { }
  User;

  ngOnInit() {

     this.ActivatedRoutes.paramMap.subscribe((element) => {
    })


    this.SellerNestimateDetails.getSellerRequirement(this.userID,this.child).then((element)=>{

      this.AllImages = element.data().Images

    })
  }


 

}
