import { Component, OnInit } from '@angular/core';
import { element } from 'protractor';
import { CMSSERVICE } from "../../../cms.service"
@Component({
  selector: 'app-othererror',
  templateUrl: './othererror.component.html',
  styleUrls: ['./othererror.component.css','../uielements.component.css', '../../Admin-common.css']
})
export class OthererrorComponent implements OnInit {
  OtherError: any ="Other"
  AgentSignIn: any;
  UserSignIn: any;
  WrongPassword: any;
  OtherErrors;
  isLoading:boolean = false
  ExpressedNestimate: any;
  topline;
  LoweLinne;
  UpdateTerm ;
  constructor(public CMSSERVICE: CMSSERVICE) { }

  ngOnInit() {
    this.getOtherForm()
    this.getSignupTerms()
  }

  getOtherForm() {
    this.CMSSERVICE.getOtherError(this.OtherError).then((element) => {
     this.AgentSignIn = element.data().AgentSignIn
     this.UserSignIn = element.data().UserSignIn
     this.ExpressedNestimate= element.data().ExpressedNestimate
      this.isLoading = false
      
    })

  }


 



  
updateOtherError()
{
  this.isLoading  = true
  this.OtherErrors ={
    AgentSignIn:this.AgentSignIn,
    UserSignIn:this.UserSignIn,
    ExpressedNestimate:this.ExpressedNestimate
  }
  this.CMSSERVICE.updateOtherError(this.OtherError,this.OtherErrors)
  this.getUpdateTerm()
  this.getSignupTerms()
  this.getOtherForm()
}

getSignupTerms()
{
  this.CMSSERVICE.getSigupTerms().then((element)=>{
    this.topline = element.data().topLine
    this.LoweLinne = element.data().SecondLine

  })
}
getUpdateTerm()
{
  this.UpdateTerm ={
    SecondLine: this.LoweLinne
  }
  this.CMSSERVICE.updateSigupTerms(this.UpdateTerm)
}

}
