  getBlogs() {
    this.HomeService.getBlogs().snapshotChanges().pipe(
      map(changes =>
        changes.map(c =>
          ({ id: c.payload.doc.id, ...c.payload.doc.data() })
        )
      )
    ).subscribe(agent => { this.homePageBlogs = agent });
  }


