  getallitems() {
    this.isLoading = true
    this.viewResult = true
    this.HomeService.getlistingBuyer(this.uid).then((res) => {
      res.forEach(element => {
        this.isLoading = true
        this.listingBuyers.push(element.data())
      });

      this.HomeService.getlistingseller(this.uid).then((res) => {
        res.forEach(elements => {
          this.isLoading = true
          this.listingSeller.push(elements.data())
        })
        this.isLoading = true

        this.checkNumberNestimates = this.listingBuyers.length + this.listingSeller.length
        this.isLoading = true
   
        if (this.checkNumberNestimates > 0) {
          this.isLoading = true
          this.appMatches = true
          this.interval = setInterval(() => { this.isLoading = false },3000)
        }
        else {
          this.isLoading = true
          this.viewResult = false
          this.isLoading = false
        }
      })
    })
  }








 
