import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-buyerrecentlydetail',
  templateUrl: './buyerrecentlydetail.component.html',
  styleUrls: ['./buyerrecentlydetail.component.css',"../../../../Misc/CommonCode/seeDetails.css" ]
})
export class BuyerrecentlydetailComponent implements OnInit {
  userDetails:any
  BuyerLookingpostcode: any;
  LookingStreetname: any;
  buyerPropertyFor: any;
  MinAmount: any;
  MaxAmount: any;
  buyingPosition: any;
  buyerFinancialPosition: any;
  BuyerPropertyType: any;
  BuyerRoommin: any;
  BuyerRoomsmax: any;
  constructor(private _router: Router) { }

  ngOnInit() {
    this.userDetails = JSON.parse(localStorage.getItem("BuyerListingDetail"))
    this.BuyerLookingpostcode  = this.userDetails.Lookkingpostcode
    this.LookingStreetname = this.userDetails.LookingStreetname
    this.buyerPropertyFor = this.userDetails.PropertyFor
    this.MinAmount = this.userDetails.MinAmount
    this.MaxAmount = this.userDetails.MaxAmount
    this.buyingPosition = this.userDetails.Position
    this.buyerFinancialPosition = this.userDetails.FinancialPosition
    this.BuyerPropertyType  = this.userDetails.PropertyType
    this.BuyerLookingpostcode = this.userDetails.Lookingpostcode
    this.BuyerRoommin = this.userDetails.Roommin
    this.BuyerRoomsmax = this.userDetails.Roomsmax
  }

  backClicked() {
    localStorage.removeItem("BuyerListingDetail");
    this._router.navigate(["/myListing/recent/tabdrop"])
  }

}