  bellone() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;
    this.confirmInterests = []
    this.confirmNotitfication = []
    this.NavigationService.Notification(this.uid).subscribe((notification) => {
      notification.forEach((element) => {
        if(element.data().Lastseen  == null)
        {
        this.confirmInterests.push({ Detail: element.data(), id: element.id })
        if (this.confirmInterests.length > 0) {
          this.notificationoverlay = true
          this.bellicontwo = true;
        }
      }
      else
      {
        this.confirmNotitfication.push({ Detail:element.data(), id: element.id });
        if (this.confirmNotitfication.length > 0) {
         this.outside = true
          this.notificationoverlay = true
          this.bellicontwo = true;
        }
      }

      })
    })
  }
  belltwo() {

    this.notificationoverlay = false
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;
    this.confirmInterests = null

    this.NavigationService.Notification(this.uid).subscribe((notification) => {
      notification.forEach((element) => {
        if (element.UserId == this.uid) {
          this.confirmInterests = null
        }
      })
    })
    this.bellicontwo = false
  }

