  async  signUp(displayName, email, pass, User) {


    var patt = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    var res = patt.test(pass);

    if(re.test(String(email).toLowerCase()) == false)
    {
     this.emailpop = true
    }
   else if(res == false)
    {
      this.passwordpop = true
    }    
    else
    {
     this.isLoading = true;
    try {
      const result = await this.afAuth.auth.createUserWithEmailAndPassword(email,pass);
     this.authService.SetUserDatas(result.user,displayName,User);
     this.authService.createUserChecker(result.user,User)
     this.isLoading = false;
     this.isLoggedIn = true;
     this.overlay = true
    } 
    catch (error) {
      window.alert(error.message);
      this.isLoading = false;
    }
    }
   
  }

