import { Component, OnInit } from '@angular/core';
import { Location } from "@angular/common";
import { Router, ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-sellerrecentpropertydeatil',
  templateUrl: './sellerrecentpropertydeatil.component.html',
  styleUrls: ['./sellerrecentpropertydeatil.component.css',"../../../../Misc/CommonCode/seeDetails.css"]
})
export class SellerrecentpropertydeatilComponent implements OnInit {
  userDetail: any;
  LookingStreetname: any;
  Lookingpostcode: any;
  MaxAmount: any;
  Maxrooms: any;
  PropertyFor: any;
  PropertyType: any;
  ownership: any;
  PropertyCondition: any;
  ImagesOpen:boolean = false
  constructor(private _location: Location, private _router: Router) { }

  ngOnInit() {

   this.userDetail = JSON.parse(localStorage.getItem("SellerListingDetail") )
   this.LookingStreetname = this.userDetail.LookingAddress
   this.Lookingpostcode = this.userDetail.Lookingpostcode
   this.MaxAmount = this.userDetail.MaxAmount
   this.Maxrooms  = this.userDetail.Maxrooms
   this.PropertyFor  = this.userDetail.PropertyFor
   this.PropertyType = this.userDetail.PropertyType
   this.ownership  = this.userDetail.ownership
   this.PropertyCondition = this.userDetail.PropertyCondition
console.log(   this.userDetail.Images)
  }
  backClicked() {
    localStorage.removeItem("SellerListingDetail");
    this._router.navigate(["/myListing/recent/tabdrop"])
  }
}
