import { Component, OnInit } from '@angular/core';
import {HomeService} from "../Home/home.service"
@Component({
  selector: 'app-home-check',
  templateUrl: './home-check.component.html',
  styleUrls: ['./home-check.component.css']
})
export class HomeCheckComponent implements OnInit {
  selectedIndex = 0;
  maxNumberOfTabs = 2;
  isLoggedIn: Boolean = false;
  userData: any;
  boolean: boolean;
  isLoading: boolean = false;
  cookie: boolean = true;
  owner: any;
  seeker: any;
  value: any
  private _user: firebase.User;
  home: any;
  home2: any;
  Policy: void;
  checkPolicy: any;
  propertyRequirementDetails: any = [];
  propertyDetails: any = [];
  propertyLength: any;
  sellerLength: any;
  uid: any;
  checkMatches: boolean = false;
  checkNumberNestimates: any;
  buyerPoperty: any;
  sellerProperty: any;
  appMatches: boolean = false
  Componame;
  user: any;
  homePageImage;
  imageUrl;
  secondImageUrl;
  TopImageText: any;
  SecondImageText: any;
  step3Text: any;
  step3Image: any;
  Step2Image: any;
  Step2Text: any;
  Step1Image: any;
  Step1TExt: any;
  how: any;
  homePageBlogs = [];
  listingBuyers = [];
  listingSeller = [];
  notificationContent: any;
  KnowUser = []
  type: any = "User"
  sellerOverlay: boolean = false
  loginTop: boolean = false
  SignUp: boolean = false
  passs: any;
  emails: string;

  formType;
  hide:boolean = true
  topLine: any;
  secondLine: any;
  name: any;

  OtherError: any ="Other"
  AgentSignIn: any;
  UserSignIn: any;
  WrongPassword: any;
  passwordpop:boolean = false
  emailpop:boolean = false
  interval;
  viewResult:boolean = false
  constructor(    public HomeService: HomeService,) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    if (this.user != null) {
      this.uid = this.user.uid;
      this.getallitems()
    }
    else
    {
      this.viewResult = false
    }
  
  }
  getallitems() {
    this.isLoading = true
    this.viewResult = true
    this.HomeService.getlistingBuyer(this.uid).then((res) => {
      res.forEach(element => {
        this.isLoading = true
        this.listingBuyers.push(element.data())
      });

      this.HomeService.getlistingseller(this.uid).then((res) => {
        res.forEach(elements => {
          this.isLoading = true
          this.listingSeller.push(elements.data())
        })
        this.isLoading = true

        this.checkNumberNestimates = this.listingBuyers.length + this.listingSeller.length
        this.isLoading = true
   
        if (this.checkNumberNestimates > 0) {
          this.isLoading = true
          this.appMatches = true
          this.interval = setInterval(() => { this.isLoading = false },3000)
        }
        else {
          this.isLoading = true
          this.viewResult = false
          this.isLoading = false
        }
      })
    })
  }
}
