    signIn(email, pass) {
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

      if(re.test(String(email).toLowerCase()) ==false)
      {
       this.emailpop = true
      }
      else{
      this.isLoading = true;

      this.CMSSERVICE.getuserType(email).then((element) => {
        if(element.data() != undefined){
        if(element.data().UserType == "Agent" || element.data().UserType == "Admin")
        {
          const dialogRef = this.dialog.open(AlertUserTypeComponent, {
            data: { message: this.UserSignIn }
          });
          this.isLoading = false;
        }
        else  {
       
          this.authService.SignIn(email, pass).then((data) => {
            this.isLoading = false;
            this.getUserKnow()
  
          });
        }
      }
         else  {
       
          const dialogRef = this.dialog.open(AlertUserTypeComponent, {
            data: { message: "User Does not exist"}
          });
          }
         
      
        });
   
    }
    }
  
    async signUp(displayName, email, pass, User) {


      var patt = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      var res = patt.test(pass);
  
      if(re.test(String(email).toLowerCase()) == false)
      {
       this.emailpop = true
      }
     else if(res == false)
      {
        this.passwordpop = true
      }    
      else
      {
      this.isLoading = true;
      try {
        const result = await this.afAuth.auth.createUserWithEmailAndPassword(email, pass);
        this.authService.SetUserDatas(result.user, displayName, User);
        this.authService.createUserChecker(result.user,User)
        this.isLoading = false;
       
        if(result.user != null)
        {
          this.isLoggedIn = true;
          this.sellerOverlay = true
  
        }
      }
      catch (error) {
        window.alert(error.message);
        this.isLoading = false;
      }
    }
    }
   
  
