import { Component,  OnInit, Inject } from '@angular/core';
import { VERSION, MatDialogRef, MatDialog, MatSnackBar, MAT_DIALOG_DATA } from '@angular/material'
import { Location } from "@angular/common";
@Component({
  selector: 'app-alert-selected-property',
  templateUrl: './alert-selected-property.component.html',
  styleUrls: ['./alert-selected-property.component.css']
})
export class AlertSelectedPropertyComponent implements OnInit {

  constructor(
    @Inject(MAT_DIALOG_DATA) private data: any,
    private _location: Location,
    public dialogRef: MatDialogRef<AlertSelectedPropertyComponent>) {
      this.dialogRef.updateSize('300vw','300vw')
     }
    
  ngOnInit() {
  }

  onConfirmClick(): void {
    this._location.back();
    this.dialogRef.close(true);
   
  }
}
