import { Component, OnInit } from '@angular/core';
import { Location } from "@angular/common";
import { Router, ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-seller-listing-detail',
  templateUrl: './seller-listing-detail.component.html',
  styleUrls: ['./seller-listing-detail.component.css',"../../../../Misc/CommonCode/seeDetails.css"]
})
export class SellerListingDetailComponent implements OnInit {
  userDetail: any;
  LookingStreetname: any;
  Lookingpostcode: any;
  MaxAmount: any;
  Maxrooms: any;
  PropertyFor: any;
  PropertyType: any;
  ownership: any;
  PropertyCondition: any;

  constructor(private _location: Location, private _router: Router) { }

  ngOnInit() {

   this.userDetail = JSON.parse(localStorage.getItem("SellerListingDetail") )
   this.LookingStreetname = this.userDetail.PropertyDetail.LookingStreetname 
   this.Lookingpostcode = this.userDetail.PropertyDetail.Lookingpostcode
   this.MaxAmount = this.userDetail.PropertyDetail.MaxAmount
   this.Maxrooms  = this.userDetail.PropertyDetail.Maxrooms
   this.PropertyFor  = this.userDetail.PropertyDetail.PropertyFor
   this.PropertyType = this.userDetail.PropertyDetail.PropertyType
   this.ownership  = this.userDetail.PropertyDetail.ownership
   this.PropertyCondition = this.userDetail.PropertyDetail.PropertyCondition

  }
  backClicked() {
    localStorage.removeItem("SellerListingDetail");
    this._router.navigate(["/myListing/fav/tabdrop"])
  }
}
