  //getUser(uid)

  signIn(email, pass) {

    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (re.test(String(email).toLowerCase()) == true) {
      this.isLoading = true;
      if (this.adminId == email) {
        this.AuthService.SignIn(email, pass).then((data) => {
          this.isLoading = false;
          this.LoggedIn = true
          window.location.reload()
        });
      }
      else {
        this.LoggedIn = false
        this.isLoading = false;
        const dialogRef = this.dialog.open(AlertUserTypeComponent, {
          data: { message: "Only users with role 'Administrator' are allowed access to this panel" }
        });
        this.isLoading = false;
      }




    }

    else {
      this.LoggedIn = false
      this.isLoading = false;
      const dialogRef = this.dialog.open(AlertUserTypeComponent, {
        data: { message: "Enter valid email Address" }
      });



      this.isLoading = false;
    }


  }


